import dayjs from 'dayjs';
import React, { createContext, useState, ReactNode, useContext } from 'react';
import { useDetectClickOutside } from 'react-detect-click-outside';
import { IAutoComplete } from '../../AutoComplete';
import { DateRangePickerSelecting } from '../../DateRangePicker';
import { PassengerPickerLabels, PassengerPickerRules, PassengerPickerValues } from '../../PassengerPicker';
import { collectData, ElquartoApiValue, FormatDateTo, GroupOptionsType, PersistAndReplaceUrlParams } from '../../utils';
import {
  ELQUARTO_DEFAULT_AMOUNT_ADULT,
  ELQUARTO_DEFAULT_AMOUNT_ROOM,
  ELQUARTO_MAX_ADULT,
  ELQUARTO_MAX_CHILD,
  ELQUARTO_MAX_ROOM,
  ELQUARTO_MIN_ADULT,
  ELQUARTO_MIN_CHILD,
  ELQUARTO_MIN_ROOM,
} from '../../variables';

interface IElquartoContextProps {
  rooms: number;
  adults: number;
  childs: number;
  start: any;
  end: any;
  startLimit: Date;
  startError: string;
  childsAgeError: string;
  childsAge: number[];
  endError: string;
  destino: IAutoComplete.Value | null;
  destinoError: string;
  destinoInput: string;
  calendarOpen: boolean;
  destinoOptions: IAutoComplete.CategoryOptions[];
  PassengersTotal: number;
  CalendarRef: any;
  passengerPickerValues: PassengerPickerValues;
  disableIncrement: PassengerPickerRules;
  disableDecrement: PassengerPickerRules;
  infoLabel: PassengerPickerLabels;
  selecting?: DateRangePickerSelecting;
  datePickerModalText: string;
  frameTitle: string;
  setSelecting: (val: DateRangePickerSelecting) => void;
  setStart: (start: any) => void;
  setEnd: (end: any) => void;
  setStartError: (error: string) => void;
  setEndError: (error: string) => void;
  setAdults: (val: number) => void;
  setChilds: (val: number) => void;
  setChildsAgeError: (val: string) => void;
  setChildsAge: (val: number[]) => void;
  setCalendarOpen: (val: boolean) => void;
  setDestino: (val: IAutoComplete.Value | null) => void;
  setDestinoError: (error: string) => void;
  setDestinoInput: (val: string) => void;
  setDestinoOptions: (options: IAutoComplete.CategoryOptions[]) => void;
  RedirectUrl: () => void;
  formatCheckinValue: () => string;
  RequestSuggestions: (value: string, setOptions: (options: any) => void) => void;
}

type ElquartoProviderProps = {
  children: ReactNode;
  url: string;
  persistUrlParams?: boolean;
  frameTitle: string;
};

export const ElquartoContext = createContext<IElquartoContextProps>({} as any);
export const UseElquarto = () => useContext(ElquartoContext);
export function ElquartoProvider({ children, url, persistUrlParams, frameTitle }: ElquartoProviderProps) {
  const [start, setStart] = useState<any>(undefined);
  const [end, setEnd] = useState<any>(undefined);
  const [startError, setStartError] = useState('');
  const [endError, setEndError] = useState('');
  const startLimit = new Date();
  const [rooms, setRooms] = useState(ELQUARTO_DEFAULT_AMOUNT_ROOM);
  const [adults, setAdults] = useState(ELQUARTO_DEFAULT_AMOUNT_ADULT);
  const [childs, setChilds] = useState(0);
  const [childsAge, setChildsAge] = useState<number[]>([]);
  const [childsAgeError, setChildsAgeError] = useState('');
  const [calendarOpen, setCalendarOpen] = useState(false);
  const [destino, setDestino] = useState<any>(null);
  const [selecting, setSelecting] = useState<DateRangePickerSelecting | undefined>(undefined);
  const [destinoError, setDestinoError] = useState('');
  const [destinoInput, setDestinoInput] = useState('');
  const [destinoOptions, setDestinoOptions] = useState<IAutoComplete.CategoryOptions[]>([]);
  const CalendarRef = useDetectClickOutside({
    onTriggered: () => setCalendarOpen(false),
  });
  const PassengersTotal = adults + childs;
  const datePickerModalText = (function () {
    let text = 'Escolha a data de ';
    switch (selecting) {
      case 'first':
        text += 'check-In';
        return text;
      case 'last':
        text += 'check-Out';
        return text;
    }
    if (!start && !end) {
      text += 'check-In';
      return text;
    }
    if (start && !end) {
      text += 'check-Out';
      return text;
    }
    return text;
  })();

  const disableDecrement: PassengerPickerRules = {
    rooms: rooms <= ELQUARTO_MIN_ROOM,
    adults: adults <= ELQUARTO_MIN_ADULT,
    childs: childs <= ELQUARTO_MIN_CHILD,
  };

  const disableIncrement: PassengerPickerRules = {
    rooms: rooms >= ELQUARTO_MAX_ROOM,
    adults: adults >= ELQUARTO_MAX_ADULT,
    childs: childs >= ELQUARTO_MAX_CHILD,
  };

  const infoLabel: PassengerPickerLabels = {
    childs: 'De 0 a 17 anos',
    adults: 'Acima de 17 anos',
  };

  const passengerPickerValues: PassengerPickerValues = {
    rooms,
    adults,
    childs,
    setAdults,
    setChilds,
    setRooms,
  };

  function VerifyFields(): boolean {
    let hasErrors = false;
    if (!start) {
      setStartError('Informe o periodo da hospedagem');
      hasErrors = true;
    }
    if (!end) {
      setEndError('Selecione a data de check-out');
      hasErrors = true;
    }
    if (!destino?.value) {
      setDestinoError('Informe o destino');
      hasErrors = true;
    }
    if (Object.values(childsAge).length < childs) {
      setChildsAgeError('Preencha as idades');
      hasErrors = true;
    }

    // @ts-ignore
    if (childsAge.includes(undefined)) {
      setChildsAgeError('Preencha as idades');
      hasErrors = true;
    }

    return hasErrors;
  }
  function RedirectUrl() {
    if (!VerifyFields()) {
      const apiData: ElquartoApiValue = destino.props;
      const formatStartDate = FormatDateTo(start, 'YYYY-MM-DD');
      const formatEndDate = FormatDateTo(end, 'YYYY-MM-DD');
      const destinationName = apiData.name['pt-BR'];
      const startDate = `startDate=${formatStartDate}&`;
      const endDate = `endDate=${formatEndDate}&`;
      const destination = `destination=${apiData.destinationId}&`;
      const encodedDestinationName = `destinationName=${encodeURIComponent(destinationName)}&`;
      const qtyRooms = `qtyRooms=${rooms}&`;
      const qtyAdult = `qtyAdult=${adults}&`;
      const qtyChildren = `qtyChildren=${childs}&`;
      const childAges = childs ? `childAges=${childsAge.join('%2C')}&` : 'childAges=&';
      const defaultParams =
        'utm_medium=afiliado&pcrtt=pp_framepesquisa&pcrid=135&utm_source=pp_framepesquisa&ppcpm=PASSPROMO';
      const urlParams = persistUrlParams ? PersistAndReplaceUrlParams(defaultParams) : defaultParams;
      const urlBuild =
        `${url}/search?` +
        startDate +
        endDate +
        destination +
        encodedDestinationName +
        qtyRooms +
        qtyAdult +
        qtyChildren +
        childAges +
        urlParams;
      window.open(urlBuild, '_blank');

      // gtag datalayer data collect
      const dataArray = [destinationName, formatStartDate, formatEndDate, rooms, adults, childs, childsAge.join(',')];
      const label = dataArray.join('|');
      collectData({
        category: 'pp-frame-pesquisa',
        action: 'pesquisa-hospedagem',
        label,
      });
    }
  }

  function formatCheckinValue() {
    if (start && end) {
      const dayStart = dayjs(start);
      const dayEnd = dayjs(end);
      const diff = dayEnd.diff(dayStart, 'days');
      return `${FormatDateTo(start, 'DD/MM/YYYY')} - ${FormatDateTo(end, 'DD/MM/YYYY')} - ${diff} Noites`;
    }
    if (start && !end) {
      return `${FormatDateTo(start, 'DD/MM/YYYY')}`;
    }
    return '';
  }
  // this is where you make your api call
  async function RequestSuggestions(value: string, _: (options: any) => void) {
    if (value.length < 3) {
      setDestinoOptions([]);
      return;
    }

    if (value.length >= 3) {
      const data = await fetch(`https://api.elquarto.com/api/v1/utils/destination/${value}`);
      const jsonData = await data.json();
      const options = GroupOptionsType(jsonData);
      setDestinoOptions(options);
    }
  }

  const DefaultState: IElquartoContextProps = {
    adults,
    calendarOpen,
    CalendarRef,
    childs,
    childsAge,
    childsAgeError,
    destino,
    destinoError,
    destinoInput,
    destinoOptions,
    disableDecrement,
    disableIncrement,
    end,
    endError,
    rooms,
    infoLabel,
    passengerPickerValues,
    PassengersTotal,
    start,
    startError,
    startLimit,
    selecting,
    datePickerModalText,
    frameTitle,
    setSelecting,
    formatCheckinValue,
    RedirectUrl,
    RequestSuggestions,
    setAdults,
    setCalendarOpen,
    setChilds,
    setChildsAge,
    setChildsAgeError,
    setDestino,
    setDestinoError,
    setDestinoInput,
    setDestinoOptions,
    setEnd,
    setEndError,
    setStart,
    setStartError,
  };

  return <ElquartoContext.Provider value={DefaultState}>{children}</ElquartoContext.Provider>;
}
