import styled from 'styled-components';

type DatePickerContainerProps = {
  mobile?: boolean;
  multiple?: boolean;
};

type WrapperProps = {
  autoPositionCalendar?: boolean;
};

export const Wrapper = styled.div<WrapperProps>`
  position: relative;
  top: -0.4rem;
  left: 0.5rem;
  @media (max-height: 847px) {
    top: ${(props) => (props.autoPositionCalendar ? '-24rem' : '-0.4rem')};
  }
  transition: 0.3s;
`;

export const Container = styled.div<DatePickerContainerProps>`
  background-color: ${(p) => (p.mobile ? '#fff' : '')};
  z-index: ${(p) => (p.mobile ? 1000 : 3)};
  position: ${(p) => (p.mobile ? 'fixed' : 'absolute')};
  width: ${(p) => (p.mobile ? '100%' : '')};
  height: ${(p) => p.mobile && '100%'};
  top: ${(p) => p.mobile && 0};
  left: ${(p) => p.mobile && 0};
`;

export const RangeContainer = styled.div`
  height: 95%;
`;

export const ModalHeader = styled.div`
  width: 100%;
  height: 8vh;
  background-color: #008bff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem;
`;

export const ModalHeaderTitle = styled.span`
  color: #fff;
  font-size: 16px;
`;

export const ModalClose = styled.div`
  cursor: pointer;
  width: 1.2rem;
  height: 1.2rem;
  z-index: 4;
`;
