import React from 'react';
import { Passagem } from '../../SearchFrame/SearchFrame/Passagem';
import { Container, Frame } from './styles';

type PassagemFrameThin = {
  SearchUrl: string;
  multipleCalendars?: boolean;
  mobile?: boolean;
};

export function PassagemFrameThin({ multipleCalendars, SearchUrl, mobile }: PassagemFrameThin) {
  return (
    <Container>
      <Frame hideProducts={true}>
        <Passagem
          size="THIN"
          url={SearchUrl}
          mobile={mobile}
          multipleCalendars={multipleCalendars}
          frameTitle="Voe mais por muito menos"
        />
      </Frame>
    </Container>
  );
}
