import React from 'react';
import { SegurosProvider } from './context';
import { SegurosThin } from './thin';
import { SegurosWide } from './wide';

type ElquartoProps = {
  size: 'WIDE' | 'THIN';
  url: string;
  mobile?: boolean;
  multipleCalendars?: boolean;
  autoPositionCalendar?: boolean;
  persistUrlParams?: boolean;
  frameTitle: string;
};

export function Seguros({ size, url, persistUrlParams, frameTitle, ...rest }: ElquartoProps) {
  return (
    <SegurosProvider frameTitle={frameTitle} url={url} persistUrlParams={persistUrlParams}>
      {size === 'WIDE' ? <SegurosWide {...rest} /> : <SegurosThin {...rest} />}
    </SegurosProvider>
  );
}
