import React from 'react';
import { ElquartoProvider } from './context';
import { ElquartoThin } from './thin';
import { ElquartoWide } from './wide';

type ElquartoProps = {
  size: 'WIDE' | 'THIN';
  url: string;
  mobile?: boolean;
  multipleCalendars?: boolean;
  autoPositionCalendar?: boolean;
  persistUrlParams?: boolean;
  frameTitle: string;
};

export function Elquarto({ size, url, persistUrlParams, frameTitle, ...rest }: ElquartoProps) {
  return (
    <ElquartoProvider frameTitle={frameTitle} url={url} persistUrlParams={persistUrlParams}>
      {size === 'WIDE' ? <ElquartoWide {...rest} /> : <ElquartoThin {...rest} />}
    </ElquartoProvider>
  );
}
