import React, { ReactNode, useEffect, useState } from 'react';
import { ErrorMessage } from '../ErrorMessage';
import { CloseIcon, SmallCloseIcon } from '../icons';
import * as S from './styles';

export declare namespace IAutoComplete {
  type Value = {
    value: any;
    label: string;
    isDisabled?: boolean;
  };

  type CategoryOptions = {
    categoryName: string;
    categoryIconUrl: ReactNode;
    categoryColor: string;
    categoryLabelColor: string;
    options: Value[];
  };
}

type AutoCompleteModalProps = {
  id?: string;
  label?: string;
  modalLabel?: string;
  clearable?: boolean;
  value: IAutoComplete.Value | null;
  setValue: (value: IAutoComplete.Value | null) => void;
  onInputChange: (change: string) => void;
  onFocus?: () => void;
  options: IAutoComplete.CategoryOptions[];
  setOptions: (options: any) => void;
  placeholder?: string;
  leftIcon?: ReactNode;
  joinLeft?: boolean;
  joinRight?: boolean;
  hasError?: boolean;
  errorMessage?: string;
};

export function AutoCompleteModal({
  leftIcon,
  id,
  joinLeft,
  joinRight,
  label,
  modalLabel,
  value,
  placeholder,
  options,
  hasError,
  errorMessage,
  clearable,
  setOptions,
  onFocus,
  onInputChange,
  setValue,
}: AutoCompleteModalProps) {
  const [tempValue, setTempValue] = useState('');
  const [focused, setFocused] = useState(false);
  const parsedOptions = ParseOptionsToCategoryAndOptions(options);

  function StripLabelIata(label: string) {
    const splitValue = label.split('(');
    if (splitValue.length < 2)
      return {
        label,
        iata: '',
      };
    const labelText = splitValue[0];
    const iataText = '(' + splitValue[1];
    return {
      label: labelText,
      iata: iataText,
    };
  }

  // calm down, it looks harder than it is
  function ParseOptionsToCategoryAndOptions(options: IAutoComplete.CategoryOptions[]) {
    // array for receiving all options
    let AllOptions: IAutoComplete.Value[] = [];
    // here we loop on all options creating a category for each one
    options.forEach((opt) => {
      // the first value in the array is a disabled option, to act as a category
      const CategoryArray = [
        {
          value: {
            categoryColor: opt.categoryColor,
            categoryLabelColor: opt.categoryLabelColor,
            categoryIconUrl: opt.categoryIconUrl,
          },
          label: opt.categoryName,
          isDisabled: true,
        },
        // then we spread the actual options
        ...opt.options,
      ];
      // and finally we join this section with the rest of the options
      AllOptions = [...AllOptions, ...CategoryArray];
    });
    return AllOptions;
  }

  function RenderCustomOption(props: IAutoComplete.Value, index: number) {
    // i added this ts-ignore, because it says that values doesnt exist, but it does
    const { isDisabled } = props;
    // disabled just means that its not an option, but an category
    return isDisabled ? RenderCategory(props, index) : RenderOption(props, index);
  }

  function RenderCategory(props: IAutoComplete.Value, index: number) {
    const { label, value } = props;
    return (
      <S.CustomCategory key={index} style={{ backgroundColor: value.categoryColor }}>
        {value?.categoryIconUrl && value.categoryIconUrl}
        <S.CustomOptionCategory color={value.categoryLabelColor}>{label}</S.CustomOptionCategory>
      </S.CustomCategory>
    );
  }

  function RenderOption(props: IAutoComplete.Value, index: number) {
    const { label } = props;
    const text = StripLabelIata(label);
    return (
      <S.CustomOptionContainer
        key={index}
        onClick={() => {
          OnOptionSelected(props);
        }}
      >
        <S.CustomOption>
          {text.label} <strong>{text.iata}</strong>
        </S.CustomOption>
      </S.CustomOptionContainer>
    );
  }

  function OnOptionSelected(option: IAutoComplete.Value) {
    setValue(option);
    setFocused(false);
  }

  function HandleChange(e: React.ChangeEvent<HTMLInputElement>) {
    setTempValue(e.target.value);
    onInputChange(e.target.value);
  }

  function HandleFocused(e: React.FocusEvent<HTMLInputElement, Element>) {
    onFocus && onFocus();
    setFocused(true);
    value && setTempValue(value.label);
    e.target.select();
  }

  function onClose() {
    setFocused(false);
    setOptions([]);
  }

  function RenderHeader() {
    return (
      <S.ModalHeader>
        <S.ModalClose onClick={onClose}>{<CloseIcon />}</S.ModalClose>
      </S.ModalHeader>
    );
  }

  function RenderMenu() {
    return <S.OptionsMenu>{parsedOptions.map((option, idx) => RenderCustomOption(option, idx))}</S.OptionsMenu>;
  }

  useEffect(() => {
    focused ? (document.body.style.overflow = 'hidden') : (document.body.style.overflow = 'unset');
  }, [focused]);

  return (
    <S.InputContainer data-testid="autocomplete-modal" modalMode={focused}>
      {focused && RenderHeader()}
      {label && !focused && <S.Label>{label} </S.Label>}
      {modalLabel && focused && <S.Label modalMode={focused}>{modalLabel}</S.Label>}
      <S.InputCenter joinLeft={joinLeft} joinRight={joinRight} modalMode={focused} hasError={hasError}>
        {leftIcon && leftIcon}
        <S.InputStyled
          id={id}
          data-testid={`autocomplete-${id}`}
          hasError={hasError}
          placeholder={focused ? 'Digite 3 ou mais letras' : placeholder}
          value={focused ? tempValue : value?.label || ''}
          onChange={HandleChange}
          onBlur={() => {
            setTempValue('');
          }}
          onFocus={HandleFocused}
          autoComplete="off"
        />
        {clearable && !!value && (
          <S.InputIcon
            data-testid="clear-autocomplete"
            onClick={() => {
              setValue(null);
            }}
          >
            <SmallCloseIcon height="1rem" width="1.5rem" marginLeft="0.3rem" color="#A8A8A8" />
          </S.InputIcon>
        )}
        <ErrorMessage message={errorMessage} />
      </S.InputCenter>
      {focused && RenderMenu()}
    </S.InputContainer>
  );
}
