import React from 'react';
import * as S from '../styles';

type ChildsAgePickerProps = {
  childs: number;
  childsAge: number[];
  childsAgeError?: boolean;
  setChildsAge: (ages: number[]) => void;
};

export function ChildsAgePicker({ childs, childsAge, childsAgeError, setChildsAge }: ChildsAgePickerProps) {
  function HandleChildAgeSelected(age: number, childIndex: number) {
    let newChildsAges = [...childsAge];
    newChildsAges[childIndex] = age;
    setChildsAge(newChildsAges);
  }
  return (
    <S.ChildsAgeContainer>
      <S.ChildsAgeInfoText>Qual a idade das crianças?</S.ChildsAgeInfoText>
      {Array.from(Array(childs)).map((_, childIndex) => (
        <S.ChildsAgeInputLine key={childIndex}>
          <S.ChildsAgeLabel>Criança {childIndex + 1}</S.ChildsAgeLabel>
          <S.ChildsAgeSelect
            value={childsAge[childIndex]}
            defaultValue=""
            onChange={(e) => HandleChildAgeSelected(Number(e.target.value), childIndex)}
          >
            <option key="none" value="" disabled>
              escolha
            </option>
            {Array.from(Array(18)).map((_, optionIndex) => (
              <option key={optionIndex} value={optionIndex}>
                {optionIndex}
              </option>
            ))}
          </S.ChildsAgeSelect>
        </S.ChildsAgeInputLine>
      ))}
      {childsAgeError && <S.ErrorBox>Informe todas as idades</S.ErrorBox>}
    </S.ChildsAgeContainer>
  );
}
