import React, { ReactNode, useEffect, useRef, useState } from 'react';
import { Input } from '../Input';
import { CloseIcon } from '../icons';
import * as S from './styles';
import { useDetectClickOutside } from 'react-detect-click-outside';
import { MenuPicker } from './MenuPicker';
import { ChildsAgePicker } from './ChildsAgePicker';
import styled from 'styled-components';

const StyledButton = styled.button`
  background-color: #008bff;
  width: 100%;
  height: 72.5%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  color: #ffffff;
  border-radius: 5px;
  border: 0;
  padding: 1rem;
  user-select: none;
  &:hover {
    filter: brightness(90%);
    transition: 0.2s;
  }
`;

export type PassengerPickerValues = {
  rooms?: number;
  adults?: number;
  childs?: number;
  infants?: number;
  setRooms?: (value: number) => void;
  setAdults?: (value: number) => void;
  setChilds?: (value: number) => void;
  setInfants?: (value: number) => void;
};

export type PassengerPickerRules = {
  rooms?: boolean;
  adults?: boolean;
  childs?: boolean;
  infants?: boolean;
};

export type PassengerPickerLabels = {
  rooms?: string;
  adults?: string;
  childs?: string;
  infants?: string;
};

type PassengerPickerProps = {
  id?: string;
  value?: string;
  values: PassengerPickerValues;
  mobile?: boolean;
  modalTitle?: string;
  hasError?: boolean;
  errorMessage?: string;
  childsAge?: number[];
  setChildsAge?: (childsAge: number[]) => void;
  onFocus?: () => void;
  label?: string;
  leftIcon?: ReactNode;
  rightIcon?: ReactNode;
  joinLeft?: boolean;
  joinRight?: boolean;
  noBorderLeft?: boolean;
  noBorderRight?: boolean;
  noBorderTop?: boolean;
  noBorderBottom?: boolean;
  infoLabel?: PassengerPickerLabels;
  disableDecrement?: PassengerPickerRules;
  disableIncrement?: PassengerPickerRules;
};

export function PassengerPicker({
  mobile,
  id,
  label,
  value,
  hasError,
  errorMessage,
  leftIcon,
  rightIcon,
  joinLeft,
  joinRight,
  childsAge,
  modalTitle,
  setChildsAge,
  onFocus,
  noBorderRight,
  noBorderBottom,
  noBorderLeft,
  values,
  infoLabel,
  disableDecrement,
  disableIncrement,
}: PassengerPickerProps) {
  const ContainerRef = useDetectClickOutside({
    onTriggered: () => setVisible(false),
  });
  const dropdownRef = useRef(null);
  const { rooms = 0, adults = 0, childs = 0, infants = 0, setRooms, setAdults, setChilds, setInfants } = values;
  const [visible, setVisible] = useState(false);
  const [childsAgeError, setChildsAgeError] = useState(false);

  function handleSubmit() {
    if (childsAge) {
      if (Object.values(childsAge).length < childs) return setChildsAgeError(true);
      // @ts-ignore
      if (childsAge.includes(undefined)) return setChildsAgeError(true);
    }
    setVisible(false);
  }

  useEffect(() => {
    if (childsAge && setChildsAge) {
      const newChilds = childsAge.splice(0, childs);
      setChildsAge(newChilds);
      if (newChilds.length < 1) setChildsAgeError(false);
    }
  }, [childs]);

  useEffect(() => {
    // This makes sure you wont be scrolling the page behind the modal
    mobile && visible ? (document.body.style.overflow = 'hidden') : (document.body.style.overflow = 'unset');
  }, [visible, mobile]);

  function RenderHeader() {
    return (
      <S.ModalHeader id="Header">
        {modalTitle || 'Passageiros'}
        <S.ModalClose
          onClick={() => {
            setVisible(false);
          }}
        >
          <CloseIcon marginTop="5px" />
        </S.ModalClose>
      </S.ModalHeader>
    );
  }

  return (
    <S.Container
      ref={ContainerRef}
      mobile={mobile && visible}
      onFocus={() => {
        setVisible(true);
      }}
      onBlur={(e) => {
        if (!e.currentTarget.contains(e.relatedTarget || dropdownRef.current)) {
          setVisible(false);
        }
      }}
    >
      {mobile && visible && RenderHeader()}
      <S.InputContainer mobile={mobile && visible}>
        <Input
          id={id}
          label={label}
          leftIcon={leftIcon}
          rightIcon={rightIcon}
          onFocus={() => {
            onFocus && onFocus();
          }}
          rightIconClick={() => setVisible(true)}
          joinLeft={joinLeft}
          joinRight={joinRight}
          value={value || ''}
          hasError={hasError}
          errorMessage={errorMessage}
          noBorderBottom={noBorderBottom}
          noBorderRight={noBorderRight}
          noBorderLeft={noBorderLeft}
          readOnly
        />
      </S.InputContainer>
      <S.DropdownContainer ref={dropdownRef}>
        <S.DropDown visible={visible} mobile={mobile && visible}>
          <S.DropDownMenu>
            {setRooms && (
              <MenuPicker
                name="Quartos"
                info={infoLabel?.rooms || ''}
                value={rooms}
                onMinusClick={() => {
                  !disableDecrement?.rooms && setRooms(rooms - 1);
                }}
                onPlusClick={() => {
                  !disableIncrement?.rooms && setRooms(rooms + 1);
                }}
                disableIncrement={disableIncrement?.rooms}
                disableDecrement={disableDecrement?.rooms}
                mobile={mobile && visible}
              />
            )}
            {setAdults && (
              <MenuPicker
                name="Adultos"
                info={infoLabel?.adults || 'acima de 11 anos'}
                value={adults}
                onMinusClick={() => {
                  !disableDecrement?.adults && setAdults(adults - 1);
                }}
                onPlusClick={() => {
                  !disableIncrement?.adults && setAdults(adults + 1);
                }}
                disableIncrement={disableIncrement?.adults}
                disableDecrement={disableDecrement?.adults}
                mobile={mobile && visible}
              />
            )}
            {setChilds && (
              <MenuPicker
                name="Crianças"
                info={infoLabel?.childs || '2 a 11 anos'}
                value={childs}
                onMinusClick={() => {
                  !disableDecrement?.childs && setChilds(childs - 1);
                }}
                onPlusClick={() => {
                  !disableIncrement?.childs && setChilds(childs + 1);
                }}
                disableIncrement={disableIncrement?.childs}
                disableDecrement={disableDecrement?.childs}
                mobile={mobile && visible}
              />
            )}
            {setInfants && (
              <MenuPicker
                name="Bebês"
                info={infoLabel?.infants || 'até 23 meses'}
                value={infants}
                onMinusClick={() => {
                  !disableDecrement?.infants && setInfants(infants - 1);
                }}
                onPlusClick={() => {
                  !disableIncrement?.infants && setInfants(infants + 1);
                }}
                disableIncrement={disableIncrement?.infants}
                disableDecrement={disableDecrement?.infants}
                mobile={mobile && visible}
              />
            )}
            {childsAge && setChildsAge && childs > 0 && (
              <ChildsAgePicker
                childs={childs}
                childsAge={childsAge}
                setChildsAge={setChildsAge}
                childsAgeError={childsAgeError}
              />
            )}
          </S.DropDownMenu>

          <S.DropDownMenuSubmit mobile={mobile && visible}>
            <StyledButton onClick={handleSubmit}>CONFIRMAR</StyledButton>
          </S.DropDownMenuSubmit>
        </S.DropDown>
      </S.DropdownContainer>
    </S.Container>
  );
}
