import React from 'react';
import { WideStyle } from './styles';
import { Input } from '../../Input';
import { DatePicker } from '../../DatePicker';
import { FormatDateTo } from '../../utils';
import { Button } from '../../Button';
import { CalendarIcon, LocationIcon, SeguroswhiteIcon } from '../../icons';
import { Select } from '../../Select';
import { ERROR_COLOR } from '../../variables';
import { UseSeguros } from './context';

type SegurosProps = {
  mobile?: boolean;
  multipleCalendars?: boolean;
  autoPositionCalendar?: boolean;
};

export function SegurosWide({ mobile, multipleCalendars, autoPositionCalendar }: SegurosProps) {
  const Context = UseSeguros();
  return (
    <WideStyle.Container>
      <WideStyle.Header>
        <WideStyle.HeaderText>{Context.frameTitle}</WideStyle.HeaderText>
        <WideStyle.HeaderImage>
          <SeguroswhiteIcon />
        </WideStyle.HeaderImage>
      </WideStyle.Header>
      <WideStyle.ContentContainer>
        <WideStyle.ContentLine>
          <Select
            id="SEGURO-DESTINO"
            option={Context.destino}
            options={Context.Options}
            setOption={(destino) => Context.setDestino(destino)}
            placeholder={'Destino'}
            leftIcon={<LocationIcon marginLeft="0.3rem" height="1.5rem" color={Context.destinoError && ERROR_COLOR} />}
            hasError={!!Context.destinoError}
            errorMessage={Context.destinoError}
            onFocus={() => {
              Context.setDestinoError('');
            }}
          />
        </WideStyle.ContentLine>
        <WideStyle.Gridline columns="2fr 2fr 1fr">
          <WideStyle.CalendarWrapper>
            <Input
              id="SEGURO-EMBARQUE"
              placeholder={'Embarque no Brasil'}
              leftIcon={<CalendarIcon marginLeft="0.3rem" height="1.4rem" color={Context.startError && ERROR_COLOR} />}
              value={Context.start ? FormatDateTo(Context.start, 'DD/MM/YYYY') : ''}
              onFocus={() => {
                Context.setEndError('');
                Context.setStartError('');
                Context.setCalendarOpen(true);
                Context.setSelecting('first');
              }}
              onBlur={(e) => {
                if (e.relatedTarget && !e.relatedTarget.contains(Context.CalendarRef.current)) {
                  Context.setCalendarOpen(false);
                }
              }}
              hasError={!!Context.startError}
              errorMessage={Context.startError}
              readOnly
              joinRight
            />
            {Context.calendarOpen && (
              <DatePicker
                autoPositionCalendar={autoPositionCalendar}
                start={Context.start}
                setStart={Context.setStart}
                end={Context.end}
                setEnd={Context.setEnd}
                startLimit={Context.startLimit}
                endLimit={Context.endLimit}
                visible={Context.calendarOpen}
                setVisible={Context.setCalendarOpen}
                mobile={mobile}
                multipleCalendars={multipleCalendars}
              />
            )}
          </WideStyle.CalendarWrapper>
          <Input
            id="SEGURO-DESEMBARQUE"
            placeholder={'Desembarque no Brasil'}
            leftIcon={<CalendarIcon marginLeft="0.3rem" height="1.4rem" color={Context.startError && ERROR_COLOR} />}
            value={Context.end ? FormatDateTo(Context.end, 'DD/MM/YYYY') : ''}
            onFocus={() => {
              Context.setEndError('');
              Context.setStartError('');
              Context.setCalendarOpen(true);
              Context.setSelecting('last');
            }}
            onBlur={(e) => {
              if (e.relatedTarget && !e.relatedTarget.contains(Context.CalendarRef.current)) {
                Context.setCalendarOpen(false);
              }
            }}
            hasError={!!Context.endError}
            errorMessage={Context.endError}
            readOnly
            joinLeft
            joinRight
            noBorderLeft
            noBorderRight
          />
          <div
            style={{
              paddingRight: '0.5rem',
              marginLeft: '-1px',
            }}
          >
            <Button id="SEGURO-PESQUISAR" variant="WIDE" onClick={Context.RedirectUrl} joinLeft>
              PESQUISAR
            </Button>
          </div>
        </WideStyle.Gridline>
      </WideStyle.ContentContainer>
    </WideStyle.Container>
  );
}
