import React, { ReactNode } from 'react';
import * as S from './styles';

type ButtonProps = {
  children: ReactNode;
  variant: 'WIDE' | 'THIN';
  onClick?: () => void;
  joinLeft?: boolean;
  rounded?: boolean;
  color?: string;
  id?: string;
};

export function Button({ joinLeft, children, rounded, color, id, variant, onClick }: ButtonProps) {
  return variant === 'THIN' ? (
    <S.ThinButton joinLeft={joinLeft} rounded={rounded} color={color} onClick={onClick} id={id}>
      {children}
    </S.ThinButton>
  ) : (
    <S.WideButton id={id} onClick={onClick}>
      {children}
    </S.WideButton>
  );
}
