import styled from 'styled-components';
import { DARK_GRAY, ERROR_COLOR, LIGHT_GRAY } from '../variables';

export const OptionsWrapper = styled.div`
  position: relative;
  margin: 0 0.5rem;
`;

type OptionsListProps = {
  joinLeft?: boolean;
  joinRight?: boolean;
};

export const OptionsList = styled.div<OptionsListProps>`
  max-height: 30rem;
  overflow-y: auto;
  position: absolute;
  margin-left: ${(props) => (props.joinLeft ? '-0.5rem' : '')};
  margin-right: ${(props) => (props.joinRight ? '-0.5rem' : '')};
  width: ${(props) => (props.joinLeft || props.joinRight ? 'calc(100% + 0.5rem)' : '100%')};
  background-color: #fff;
  top: -0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  z-index: 2;
  box-shadow: 0 3px 4px 0 rgb(0 0 0 / 50%);
  /* width */
  &::-webkit-scrollbar {
    width: 10px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: #dcdcdc;
    border-radius: 5px;
  }
`;

export const InputContainer = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
`;

type InputCenterProps = {
  joinLeft?: boolean;
  joinRight?: boolean;
  noBorderLeft?: boolean;
  noBorderRight?: boolean;
  noBorderTop?: boolean;
  noBorderBottom?: boolean;
  disabled?: boolean;
  hasError?: boolean;
};

export const InputCenter = styled.div<InputCenterProps>`
  --border-color: ${(props) => (props.hasError ? '1px solid red' : '1px solid #aaa9a9')};
  display: flex;
  position: relative;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.24rem;
  margin-right: ${(props) => (props.joinRight ? '' : '0.5rem')};
  margin-left: ${(props) => (props.joinLeft ? '' : '0.5rem')};
  border-top-right-radius: ${(props) => (props.joinRight ? '' : '5px')};
  border-bottom-right-radius: ${(props) => (props.joinRight ? '' : '5px')};
  border-top-left-radius: ${(props) => (props.joinLeft ? '' : '5px')};
  border-bottom-left-radius: ${(props) => (props.joinLeft ? '' : '5px')};
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  border-left: ${(props) => (props.noBorderLeft ? '' : 'var(--border-color)')};
  border-right: ${(props) => (props.noBorderRight ? '' : 'var(--border-color)')};
  border-top: ${(props) => (props.noBorderTop ? '' : 'var(--border-color)')};
  border-bottom: ${(props) => (props.noBorderBottom ? '' : 'var(--border-color)')};
  background-color: ${(props) => (props.disabled ? '#ddd' : props.hasError ? '#FEF3F3' : 'white')};
  user-select: none;
`;

export const Label = styled.label`
  padding-left: 1rem;
  font-size: 14px;
  font-weight: 600;
  color: #797979;
  user-select: none;
`;

type InputStyledProps = {
  hasError?: boolean;
};

export const InputStyled = styled.input<InputStyledProps>`
  width: 100%;
  font-size: 17px;
  padding: 0.62rem 0.6rem;
  background-color: ${(props) => (props.hasError ? '#fef3f3' : '')};
  color: ${DARK_GRAY};
  &::placeholder {
    font-size: 16px;
    color: ${(props) => (props.hasError ? ERROR_COLOR : LIGHT_GRAY)};
    font-weight: ${(props) => (props.hasError ? 500 : 300)};
  }
  border: none;
  &:focus {
    outline: none;
  }
  &:disabled {
    background-color: #ddd;
    cursor: not-allowed;
  }
  user-select: none;
`;

export const InputIcon = styled.div`
  cursor: ${(props) => (props.onClick ? 'pointer' : '')};
`;

