import React from 'react';
import { Input } from '../../Input';
import { DatePicker } from '../../DatePicker';
import { FormatDateTo } from '../../utils';
import { ThinStyle } from './styles';
import { CalendarIcon, LocationIcon, SeguroswhiteIcon } from '../../icons';
import { Select } from '../../Select';
import { ERROR_COLOR } from '../../variables';
import { Button } from '../../Button';
import { UseSeguros } from './context';

type SegurosProps = {
  mobile?: boolean;
  multipleCalendars?: boolean;
  autoPositionCalendar?: boolean;
};

export function SegurosThin({ mobile, multipleCalendars, autoPositionCalendar }: SegurosProps) {
  const Context = UseSeguros();
  return (
    <>
      <ThinStyle.HeaderText>
        <ThinStyle.MainText>{Context.frameTitle}</ThinStyle.MainText>
      </ThinStyle.HeaderText>
      <ThinStyle.InputForm>
        <Select
          id="SEGURO-DESTINO"
          option={Context.destino}
          options={Context.Options}
          setOption={(destino) => Context.setDestino(destino)}
          placeholder={'Destino'}
          leftIcon={<LocationIcon marginLeft="0.3rem" color={Context.destinoError && ERROR_COLOR} />}
          label="Destino da viagem"
          hasError={!!Context.destinoError}
          errorMessage={Context.destinoError}
          onFocus={() => {
            Context.setDestinoError('');
          }}
        />

        <ThinStyle.DateInputContainer ref={Context.CalendarRef}>
          <Input
            id="SEGURO-EMBARQUE"
            label="Embarque e desembarque do Brasil"
            placeholder={Context.startError ? Context.startError : 'Embarque'}
            leftIcon={<CalendarIcon marginLeft="0.3rem" color={Context.startError && ERROR_COLOR} />}
            value={Context.start ? FormatDateTo(Context.start, 'DD/MM/YYYY') : ''}
            onFocus={() => {
              Context.setStartError('');
              Context.setEndError('');
              Context.setCalendarOpen(true);
              Context.setSelecting('first');
            }}
            onBlur={(e) => {
              if (e.relatedTarget && !e.relatedTarget.contains(Context.CalendarRef.current)) {
                Context.setCalendarOpen(false);
              }
            }}
            errorMessage={Context.startError}
            hasError={!!Context.startError}
            readOnly
          />
          <Input
            id="SEGURO-DESEMBARQUE"
            placeholder={Context.endError ? Context.endError : 'Desembarque'}
            leftIcon={<CalendarIcon marginLeft="0.3rem" color={Context.endError && ERROR_COLOR} />}
            value={Context.end ? FormatDateTo(Context.end, 'DD/MM/YYYY') : ''}
            onFocus={() => {
              Context.setStartError('');
              Context.setEndError('');
              Context.setCalendarOpen(true);
              Context.setSelecting('last');
            }}
            onBlur={(e) => {
              if (e.relatedTarget && !e.relatedTarget.contains(Context.CalendarRef.current)) {
                Context.setCalendarOpen(false);
              }
            }}
            errorMessage={Context.endError}
            hasError={!!Context.endError}
            readOnly
          />
          {Context.calendarOpen && (
            <DatePicker
              autoPositionCalendar={autoPositionCalendar}
              start={Context.start}
              setStart={Context.setStart}
              end={Context.end}
              setEnd={Context.setEnd}
              startLimit={Context.startLimit}
              endLimit={Context.endLimit}
              visible={Context.calendarOpen}
              modalTitle={Context.datePickerModalText}
              isSelecting={Context.selecting}
              setIsSelecting={Context.setSelecting}
              setVisible={Context.setCalendarOpen}
              mobile={mobile}
              multipleCalendars={multipleCalendars}
            />
          )}
        </ThinStyle.DateInputContainer>
      </ThinStyle.InputForm>
      <ThinStyle.LogoContainer>
        <ThinStyle.LogoLabel>Buscando em</ThinStyle.LogoLabel>
        <ThinStyle.Logo>
          <SeguroswhiteIcon />
        </ThinStyle.Logo>
      </ThinStyle.LogoContainer>
      <ThinStyle.Submit>
        <Button id="SEGURO-PESQUISAR" variant="THIN" onClick={Context.RedirectUrl} rounded>
          PESQUISAR
        </Button>
      </ThinStyle.Submit>
    </>
  );
}
