import React from 'react';
import { MinusIcon, PlusIcon } from '../../icons';
import * as S from '../styles';

type MenuPickerProps = {
  name: string;
  info: string;
  value: number;
  disableIncrement?: boolean;
  disableDecrement?: boolean;
  onMinusClick: () => void;
  onPlusClick: () => void;
  mobile?: boolean;
};

export function MenuPicker({
  name,
  info,
  value,
  mobile,
  disableIncrement,
  disableDecrement,
  onMinusClick,
  onPlusClick,
}: MenuPickerProps) {
  return (
    <S.DropDownMenuPicker mobile={mobile}>
      <S.MenuPickerLeft>
        <S.MenuPickerLabelContainer>
          <S.MenuPickerLabel mobile={mobile}>{name}</S.MenuPickerLabel>
          <S.MenuPickerInfo mobile={mobile}>{info}</S.MenuPickerInfo>
        </S.MenuPickerLabelContainer>
      </S.MenuPickerLeft>
      <S.MenuPickerRight>
        <S.MenuPickerButton
          onClick={() => !disableDecrement && onMinusClick()}
          disabled={disableDecrement}
          mobile={mobile}
          data-testid={`remove-passenger-${name}`}
        >
          <MinusIcon color={disableDecrement ? '#A8A8A8' : ''} backgroundColor={disableDecrement ? '#ECEFF1' : ''} />
        </S.MenuPickerButton>
        <S.MenuPickerValue mobile={mobile}>{value}</S.MenuPickerValue>
        <S.MenuPickerButton
          onClick={() => !disableIncrement && onPlusClick()}
          disabled={disableIncrement}
          mobile={mobile}
          data-testid={`add-passenger-${name}`}
        >
          <PlusIcon color={disableIncrement ? '#A8A8A8' : ''} backgroundColor={disableIncrement ? '#ECEFF1' : ''} />
        </S.MenuPickerButton>
      </S.MenuPickerRight>
    </S.DropDownMenuPicker>
  );
}
