import React from 'react';
import { Input } from '../../Input';
import { DatePicker } from '../../DatePicker';
import { AutoComplete } from '../../AutoComplete';
import { ThinStyle } from './styles';
import * as Icons from '../../icons';
import * as Var from '../../variables';
import { AutoCompleteModal } from '../../AutoCompleteModal';
import { PassengerPicker } from '../../PassengerPicker';
import { Button } from '../../Button';
import { UseElquarto } from './context';

type ElquartoProps = {
  mobile?: boolean;
  multipleCalendars?: boolean;
};

export function ElquartoThin({ mobile, multipleCalendars }: ElquartoProps) {
  const Context = UseElquarto();

  return (
    <>
      <ThinStyle.HeaderText>
        <ThinStyle.MainText>{Context.frameTitle}</ThinStyle.MainText>
      </ThinStyle.HeaderText>
      <ThinStyle.InputForm>
        {mobile ? (
          <AutoCompleteModal
            id="ELQUARTO-DESTINO"
            placeholder={'Destino'}
            leftIcon={<Icons.LocationIcon marginLeft="0.3rem" color={Context.destinoError && Var.ERROR_COLOR} />}
            label="Para onde você vai?"
            modalLabel="Para onde você vai?"
            onInputChange={(change) => Context.RequestSuggestions(change, Context.setDestinoOptions)}
            onFocus={() => {
              Context.setDestinoError('');
            }}
            value={Context.destino}
            setValue={Context.setDestino}
            hasError={!!Context.destinoError}
            errorMessage={Context.destinoError}
            options={Context.destinoOptions}
            setOptions={Context.setDestinoOptions}
          />
        ) : (
          <AutoComplete
            id="ELQUARTO-DESTINO"
            placeholder={'Destino'}
            leftIcon={<Icons.LocationIcon marginLeft="0.2rem" color={Context.destinoError && Var.ERROR_COLOR} />}
            label="Para onde você vai?"
            onFocus={() => {
              Context.setDestinoError('');
            }}
            onInputChange={(change) => Context.RequestSuggestions(change, Context.setDestinoOptions)}
            option={Context.destino}
            noOptionsMessage="Digite 3 ou mais letras para pesquisar"
            setOption={Context.setDestino}
            setOptions={Context.setDestinoOptions}
            options={Context.destinoOptions}
            value={Context.destinoInput}
            setValue={Context.setDestinoInput}
            hasError={!!Context.destinoError}
            errorMessage={Context.destinoError}
            clearable
          />
        )}

        <div ref={Context.CalendarRef}>
          <Input
            id="ELQUARTO-CHECKIN-CHECKOUT"
            label="Check-in e Check-out"
            placeholder={'Insira as datas da viagem'}
            leftIcon={
              <Icons.CalendarIcon
                marginLeft="0.2rem"
                color={!!Context.endError || !!Context.endError ? Var.ERROR_COLOR : ''}
              />
            }
            value={Context.formatCheckinValue()}
            onClick={() => {
              Context.setCalendarOpen(true);
            }}
            onFocus={() => {
              Context.setStartError('');
              Context.setEndError('');
              Context.setCalendarOpen(true);
            }}
            onBlur={(e) => {
              if (e.relatedTarget && !e.relatedTarget.contains(Context.CalendarRef.current)) {
                console.log('blur');
                Context.setCalendarOpen(false);
              }
            }}
            hasError={!!Context.endError || !!Context.startError}
            errorMessage={Context.endError || Context.startError}
            readOnly
          />
          {Context.calendarOpen && (
            <DatePicker
              start={Context.start}
              setStart={Context.setStart}
              end={Context.end}
              setEnd={Context.setEnd}
              startLimit={Context.startLimit}
              visible={Context.calendarOpen}
              setVisible={Context.setCalendarOpen}
              modalTitle={Context.datePickerModalText}
              mobile={mobile}
              multipleCalendars={multipleCalendars}
            />
          )}
        </div>

        <PassengerPicker
          id="ELQUARTO-PASSENGERS"
          label="Acomodação"
          modalTitle="Hóspedes"
          mobile={mobile}
          value={`${Context.rooms} Quarto${Context.rooms > 1 ? 's' : ''} , ${Context.PassengersTotal} Hóspede${
            Context.PassengersTotal > 1 ? 's' : ''
          }`}
          leftIcon={
            <Icons.CamaIcon marginLeft="0.1rem" height="1.5rem" color={Context.childsAgeError ? Var.ERROR_COLOR : ''} />
          }
          rightIcon={<Icons.ArrowdownIcon marginRight="0.7rem" />}
          hasError={!!Context.childsAgeError}
          onFocus={() => {
            Context.setChildsAgeError('');
          }}
          childsAge={Context.childsAge}
          setChildsAge={Context.setChildsAge}
          values={Context.passengerPickerValues}
          disableDecrement={Context.disableDecrement}
          disableIncrement={Context.disableIncrement}
          infoLabel={Context.infoLabel}
          errorMessage={Context.childsAgeError}
        />
      </ThinStyle.InputForm>
      <ThinStyle.LogoContainer>
        <ThinStyle.LogoLabel>Buscando em</ThinStyle.LogoLabel>
        <ThinStyle.Logo>
          <Icons.ElquartowhiteIcon />
        </ThinStyle.Logo>
      </ThinStyle.LogoContainer>
      <ThinStyle.Submit>
        <Button id="ELQUARTO-PESQUISAR" variant="THIN" onClick={Context.RedirectUrl} rounded>
          PESQUISAR
        </Button>
      </ThinStyle.Submit>
    </>
  );
}
