import React from 'react';
import { IAutoComplete } from './AutoComplete';
import { ElquartoAviao, ElquartoCama, ElquartoCidade, LocationIcon } from './icons';
import { ERROR_COLOR } from './variables';

type FormatDateToOptions = 'DD/MM/YYYY' | 'DDMMYYYY' | 'YYMMDD' | 'YY/MM/DD' | 'YYYY-MM-DD';

/** This function concatenates every params on the current page, and if there is a duplicate info
 * It will be replaced by the param specified on the url, (url param is priority)
 */
export function PersistAndReplaceUrlParams(currentParams: string) {
  const previousParamsObject = breakAndMergeParams(currentParams);
  const urlParams = new URLSearchParams(window.location.search).toString();
  const newParamsObject = breakAndMergeParams(urlParams, previousParamsObject);
  const newParamsString = joinParamsToString(newParamsObject);
  return newParamsString;
}

function breakAndMergeParams(params: string, paramsObj?: object): object {
  const paramsObject: any = paramsObj || {};
  if (!params) return paramsObject;
  const paramsArray = params.split('&');
  paramsArray?.forEach((param) => {
    const [name, value] = param.split('=');
    paramsObject[name] = value;
  });
  return paramsObject;
}

function joinParamsToString(paramsObject: object): string {
  const paramsArray = [];
  for (const [key, value] of Object.entries(paramsObject)) {
    paramsArray.push(`${key}=${value}`);
  }
  return paramsArray.join('&');
}

export function FormatDateTo(DateString: Date, format: FormatDateToOptions): string {
  function DDMMYYYY() {
    return DateString.toLocaleDateString('pt-BR', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
    });
  }

  function YYMMDD_NoDivisors() {
    return DateString.toLocaleDateString('en-ZA', {
      year: '2-digit',
      month: '2-digit',
      day: '2-digit',
    }).replaceAll('/', '');
  }

  function YYYY_MM_DD() {
    return DateString.toLocaleDateString('en-ZA', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
    }).replaceAll('/', '-');
  }

  switch (format) {
    case 'DD/MM/YYYY':
      return DDMMYYYY();

    case 'YYMMDD':
      return YYMMDD_NoDivisors();

    case 'YYYY-MM-DD':
      return YYYY_MM_DD();

    default:
      return '';
  }
}

type suggestion = {
  value: string;
  label: string;
  isDisabled?: boolean;
};

export async function RequestSuggestionsPassagem(term: string) {
  const response = await fetch(`https://geo.emitir.com.br/autosuggest/find?intl=1&term=${term}`);
  const jsonData = await response.json();
  let result: [string, string][] = jsonData.result;
  let searchOptions: suggestion[] = result.map((option) => ({
    value: option[0],
    label: option[1],
    isDisabled: false,
  }));
  return searchOptions;
}

type ElquartoApi = ElquartoApiValue[];
export type ElquartoApiValue = {
  type: 'City' | 'Hotel' | 'Neighborhood' | 'Country' | 'Airport';
  name: {
    'pt-BR': string;
    en: string;
  };
  countryId: string;
  hotelId: number;
  destinationId: number;
};

export function GroupOptionsType(optionsApi: ElquartoApi): IAutoComplete.CategoryOptions[] {
  let options: IAutoComplete.CategoryOptions[] = [];
  let hotelGroup: IAutoComplete.CategoryOptions = {
    categoryName: 'Hoteis',
    categoryIconUrl: <ElquartoCama color="#FFF" />,
    categoryColor: '#008BFF',
    categoryLabelColor: '#fff',
    options: [],
  };
  let cityGroup: IAutoComplete.CategoryOptions = {
    categoryName: 'Cidades',
    categoryIconUrl: <ElquartoCidade color="#FFF" />,
    categoryColor: '#008BFF',
    categoryLabelColor: '#fff',
    options: [],
  };
  let pointsOfInterestGroup: IAutoComplete.CategoryOptions = {
    categoryName: 'Pontos de interesse',
    categoryIconUrl: <LocationIcon color="#FFF" />,
    categoryColor: '#008BFF',
    categoryLabelColor: '#fff',
    options: [],
  };
  let airportGroup: IAutoComplete.CategoryOptions = {
    categoryName: 'Aeroportos',
    categoryIconUrl: <ElquartoAviao color="#FFF" />,
    categoryColor: '#008BFF',
    categoryLabelColor: '#fff',
    options: [],
  };
  let countryGroup: IAutoComplete.CategoryOptions = {
    categoryName: 'Paises',
    categoryIconUrl: <ElquartoCidade color="#FFF" />,
    categoryColor: '#008BFF',
    categoryLabelColor: '#fff',
    options: [],
  };
  optionsApi?.forEach((option) => {
    const name = option.name['pt-BR'];
    switch (option.type) {
      case 'Hotel':
        hotelGroup.options.push({
          value: name,
          label: name,
          isDisabled: false,
          props: option,
        });
        break;
      case 'City':
        cityGroup.options.push({
          value: name,
          label: name,
          isDisabled: false,
          props: option,
        });
        break;
      case 'Neighborhood':
        pointsOfInterestGroup.options.push({
          value: name,
          label: name,
          isDisabled: false,
          props: option,
        });
        break;
      case 'Airport':
        airportGroup.options.push({
          value: name,
          label: name,
          isDisabled: false,
          props: option,
        });
        break;
      case 'Country':
        countryGroup.options.push({
          value: name,
          label: name,
          isDisabled: false,
          props: option,
        });
        break;

      default:
        break;
    }
  });

  cityGroup.options.length > 0 && options.push(cityGroup);
  hotelGroup.options.length > 0 && options.push(hotelGroup);
  countryGroup.options.length > 0 && options.push(countryGroup);
  airportGroup.options.length > 0 && options.push(airportGroup);
  pointsOfInterestGroup.options.length > 0 && options.push(pointsOfInterestGroup);

  if (options.length < 1) {
    options.push({
      categoryName: 'Destino inválido',
      categoryIconUrl: <LocationIcon width="1.5rem" height="1.5rem" color={ERROR_COLOR} />,
      categoryColor: '#fff',
      categoryLabelColor: ERROR_COLOR,
      options: [],
    });
  }

  return options;
}

type collectDataProps = {
  category: string;
  action: string;
  label: string;
};

//** Fires an event to dataLayer, which can be used to collect data  */
export function collectData(props: collectDataProps) {
  if (process.env.NODE_ENV === 'production') {
    /* eslint-disable */
    try {
      // @ts-ignore
      window.dataLayer.push({
        event: 'pp_event',
        event_category: props.category,
        event_action: props.action,
        event_label: props.label,
      });
    } catch (error) {
      console.log('Error while sendind data to analytics');
    }
    /*!eslint-enable */
  }
}
