import styled from 'styled-components';
import { DARK_GRAY, ERROR_COLOR, LIGHT_GRAY } from '../variables';

type ModalModeProps = {
  modalMode?: boolean;
};

export const InputContainer = styled.div<ModalModeProps>`
  display: flex;
  width: 100%;
  font-size: 16px;
  flex-direction: column;
  position: ${(props) => (props.modalMode ? 'fixed' : '')};
  top: ${(props) => (props.modalMode ? '0' : '')};
  left: ${(props) => (props.modalMode ? '0' : '')};
  height: ${(props) => (props.modalMode ? '100%' : '')};
  background-color: ${(props) => (props.modalMode ? 'white' : '')};
  z-index: ${(props) => (props.modalMode ? '1000' : '')};
`;

type InputCenterProps = {
  modalMode?: boolean;
  joinLeft?: boolean;
  joinRight?: boolean;
  hasError?: boolean;
};

export const InputCenter = styled.div<InputCenterProps>`
  --border-color: ${(props) =>
    props.hasError ? '1px solid red' : '1px solid #aaa9a9'};
  display: flex;
  position: relative;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.3rem;
  margin: ${(props) => (props.modalMode ? '0.5rem 1.5rem 0 1.5rem' : '0.5rem')};
  border: var(--border-color);
  z-index: 2;
  border-top-right-radius: ${(props) => (props.joinRight ? '' : '5px')};
  border-bottom-right-radius: ${(props) => (props.joinRight ? '' : '5px')};
  border-top-left-radius: ${(props) => (props.joinLeft ? '' : '5px')};
  border-bottom-left-radius: ${(props) => (props.joinLeft ? '' : '5px')};
  background-color: ${(props) => (props.hasError ? '#FEF3F3' : 'white')};
`;

export const Label = styled.label<ModalModeProps>`
  padding-left: 1rem;
  font-weight: ${(props) => (props.modalMode ? '400' : '600')};
  font-size: ${(props) => (props.modalMode ? '18px' : '14px')};
  color: ${(props) => (props.modalMode ? 'white' : '#797979')};
  margin-top: ${(props) => (props.modalMode ? '1rem' : '')};
  z-index: 2;
`;

export const Selectcontainer = styled.div`
  width: 100%;
`;

type InputStyledProps = {
  hasError?: boolean;
};

export const InputStyled = styled.input<InputStyledProps>`
  width: 100%;
  font-size: 17px;
  padding: 0.6rem;
  border: none;
  background-color: ${(props) => (props.hasError ? '#fef3f3' : '')};
  color: ${DARK_GRAY};
  &::placeholder {
    font-size: 16px;
    color: ${(props) => (props.hasError ? ERROR_COLOR : LIGHT_GRAY)};
    font-weight: ${(props) => (props.hasError ? 500 : 300)};
  }
  &:focus {
    outline: none;
  }
  &:disabled {
    cursor: not-allowed;
  }
`;

