import React, { Suspense } from 'react';
import * as S from './styles';
import { CloseIcon } from '../icons';
import { CustomStyles } from '../DateRangePicker/styles';
import { DateRangePicker, DateRangePickerSelecting } from '../DateRangePicker';

const weekdaysName = ['DOM', 'SEG', 'TER', 'QUA', 'QUI', 'SEX', 'SAB'];
const monthsName = [
  'Janeiro',
  'Fevereiro',
  'Março',
  'Abril',
  'Maio',
  'Junho',
  'Julho',
  'Agosto',
  'Setembro',
  'Outubro',
  'Novembro',
  'Dezembro',
];

const MobileCustomStyle: CustomStyles = {
  CalendarContainer: {
    maxWidth: '100%',
    boxShadow: '0 0 0px rgb(0 0 0 / 50%)',
  },
  HeaderContainer: {
    backgroundColor: 'white',
  },
  HeaderButton: {
    color: '#008bff',
  },
  HeaderYear: {
    color: '#008bff',
  },
  HeaderMonth: {
    color: '#008bff',
  },
};
const DesktopCustomStyle: CustomStyles = {
  HeaderYear: {
    color: '#fff',
    fontSize: '14px',
    fontWeight: 'bold',
  },
  HeaderMonth: {
    color: '#fff',
    fontSize: '14px',
    fontWeight: 'bold',
  },
};

type DatePickerProps = {
  start: Date;
  end: Date;
  startLimit?: Date;
  endLimit?: Date;
  isSelecting?: DateRangePickerSelecting;
  setIsSelecting?: (val: DateRangePickerSelecting) => void;
  setStart: (date: Date) => void;
  setEnd: (date: Date | undefined) => void;
  visible: boolean;
  modalTitle?: string;
  setVisible: (visible: boolean) => void;
  mobile?: boolean;
  multipleCalendars?: boolean;
  autoPositionCalendar?: boolean;
  singleDate?: boolean;
};

export function DatePicker({
  start,
  end,
  setStart,
  setEnd,
  startLimit,
  endLimit,
  isSelecting,
  visible,
  modalTitle,
  setIsSelecting,
  setVisible,
  singleDate,
  autoPositionCalendar,
  mobile,
  multipleCalendars,
}: DatePickerProps) {
  function CloseCalendar() {
    setVisible(false);
  }
  function RenderHeader() {
    return (
      <S.ModalHeader>
        <S.ModalHeaderTitle>{modalTitle && modalTitle}</S.ModalHeaderTitle>
        <S.ModalClose
          data-testid="close-calendar-modal"
          onClick={() => {
            CloseCalendar();
          }}
        >
          <CloseIcon />
        </S.ModalClose>
      </S.ModalHeader>
    );
  }

  return (
    <S.Wrapper autoPositionCalendar={autoPositionCalendar}>
      <S.Container mobile={mobile} multiple={multipleCalendars}>
        {mobile && RenderHeader()}
        {visible && (
          <S.RangeContainer>
            <Suspense fallback={<div></div>}>
              <DateRangePicker
                weekdaysName={weekdaysName}
                monthsName={monthsName}
                isSelecting={isSelecting}
                first={start}
                last={end}
                startDate={startLimit}
                limitDate={endLimit}
                onFirstDateSelected={(date) => {
                  setStart(date);
                  !singleDate && setIsSelecting && setIsSelecting('last');
                }}
                onLastDateSelected={(date) => {
                  setEnd(date);
                }}
                onSelectionComplete={() => {
                  setVisible(false);
                }}
                visible={visible}
                multiple={multipleCalendars && !mobile}
                singleDate={singleDate}
                customStyles={mobile ? MobileCustomStyle : DesktopCustomStyle}
              />
            </Suspense>
          </S.RangeContainer>
        )}
      </S.Container>
    </S.Wrapper>
  );
}
