import React, { CSSProperties, ReactNode, useRef, useState } from 'react';
import { useDetectClickOutside } from 'react-detect-click-outside';
import { ErrorMessage } from '../ErrorMessage';
import { ArrowdownIcon } from '../icons';
import * as S from './styles';

export declare namespace IAutoComplete {
  type Value = {
    value: any;
    label: string;
    isDisabled?: boolean;
  };

  type CategoryOptions = {
    categoryName: string;
    categoryIconUrl: ReactNode;
    categoryColor: string;
    categoryLabelColor: string;
    options: Value[];
  };
}

export interface SelectProps {
  setOption: (option: string) => void;
  onFocus?: () => void;
  onBlur?: () => void;
  leftIconClick?: () => void;
  id?: string;
  option?: string;
  options: string[];
  label?: string;
  placeholder?: string;
  disabled?: boolean;
  leftIcon?: ReactNode;
  leftIconStyle?: CSSProperties;
  joinLeft?: boolean;
  joinRight?: boolean;
  noBorderLeft?: boolean;
  noBorderRight?: boolean;
  noBorderTop?: boolean;
  noBorderBottom?: boolean;
  hasError?: boolean;
  errorMessage?: string;
}

export function Select({
  leftIcon,
  leftIconStyle,
  leftIconClick,
  label,
  id,
  joinLeft,
  joinRight,
  noBorderBottom,
  noBorderLeft,
  noBorderRight,
  noBorderTop,
  disabled,
  option,
  options,
  setOption,
  onFocus,
  hasError,
  errorMessage,
  placeholder,
}: SelectProps) {
  const [focused, setFocused] = useState(false);
  const ListRef = useRef(null);
  const InputRef = useDetectClickOutside({
    onTriggered: () => setFocused(false),
  });

  function RenderOption(value: string, index: number) {
    return (
      <S.CustomOptionContainer
        key={index}
        onClick={() => {
          setOption(value);
          setFocused(false);
        }}
      >
        <S.CustomOption>{value}</S.CustomOption>
      </S.CustomOptionContainer>
    );
  }

  return (
    <S.InputContainer style={{ zIndex: focused ? 5 : 0 }} ref={InputRef}>
      {label && <S.Label>{label} </S.Label>}
      <S.InputCenter
        joinLeft={joinLeft}
        joinRight={joinRight}
        noBorderLeft={noBorderLeft}
        noBorderRight={noBorderRight}
        noBorderTop={noBorderTop}
        noBorderBottom={noBorderBottom}
        disabled={disabled}
        hasError={hasError}
      >
        {leftIcon && (
          <S.InputIcon style={leftIconStyle} onClick={leftIconClick}>
            {leftIcon}
          </S.InputIcon>
        )}
        <S.InputStyled
          id={id}
          placeholder={placeholder}
          value={option}
          hasError={hasError}
          disabled={disabled}
          onClick={() => setFocused(true)}
          onFocus={() => {
            onFocus && onFocus();
          }}
          onBlur={(e) => {
            if (e.relatedTarget && !e.relatedTarget.contains(ListRef.current)) {
              setFocused(false);
            }
          }}
          readOnly
        />

        <S.InputIcon onClick={() => setFocused(!focused)}>
          <ArrowdownIcon marginRight="0.7rem" />
        </S.InputIcon>

        <ErrorMessage message={errorMessage} />
      </S.InputCenter>
      <S.OptionsWrapper ref={ListRef} style={{ display: focused ? '' : 'none' }}>
        <S.OptionsList joinLeft={joinLeft} joinRight={joinRight}>
          {options.map((option, idx) => RenderOption(option, idx))}
        </S.OptionsList>
      </S.OptionsWrapper>
    </S.InputContainer>
  );
}
