import React, { useEffect } from 'react';
import { Input } from '../../Input';
import { DatePicker } from '../../DatePicker';
import { AutoComplete } from '../../AutoComplete';
import { ThinStyle } from './styles';
import { AutoCompleteModal } from '../../AutoCompleteModal';
import { Button } from '../../Button';
import { PassengerPicker } from '../../PassengerPicker';
import { ArrowdownIcon, CalendarGrayIcon, CalendarIcon, LocationIcon, PassengerIcon, PlaneIcon } from '../../icons';
import { ERROR_COLOR } from '../../variables';
import { UsePassagem } from './context';

type PassagensProps = {
  mobile?: boolean;
  multipleCalendars?: boolean;
  autoPositionCalendar?: boolean;
};

export function PassagemThin({ mobile, multipleCalendars, autoPositionCalendar }: PassagensProps) {
  const Context = UsePassagem();

  useEffect(() => {
    // This makes sure you wont be scrolling the page behind the modal
    Context.calendarOpen && mobile
      ? (document.body.style.overflow = 'hidden')
      : (document.body.style.overflow = 'unset');
  }, [Context.calendarOpen, mobile]);

  useEffect(() => {
    if (Context.travelType === 'IDA') {
      Context.setEnd(undefined);
      Context.setEndError('');
    }
  }, [Context.travelType]);

  return (
    <>
      <ThinStyle.HeaderText>
        <ThinStyle.MainText>{Context.frameTitle}</ThinStyle.MainText>
      </ThinStyle.HeaderText>

      <ThinStyle.InputForm>
        <ThinStyle.TravelTypes>
          <ThinStyle.Travel
            id="PASSAGEM-IDAVOLTA"
            onClick={() => Context.setTravelType('IDAVOLTA')}
            selected={Context.travelType === 'IDAVOLTA'}
          >
            Ida e volta
          </ThinStyle.Travel>
          <ThinStyle.Travel
            id="PASSAGEM-SOIDA"
            onClick={() => {
              Context.setTravelType('IDA');
              Context.setEndError('');
            }}
            selected={Context.travelType === 'IDA'}
          >
            Só ida
          </ThinStyle.Travel>
        </ThinStyle.TravelTypes>
        {mobile ? (
          <AutoCompleteModal
            id="PASSAGEM-ORIGEM"
            placeholder={'Origem'}
            leftIcon={
              <LocationIcon
                height="1rem"
                width="1rem"
                marginLeft="0.3rem"
                marginRight="0.3rem"
                color={Context.origemError && ERROR_COLOR}
              />
            }
            label="Partindo de onde?"
            modalLabel="Partindo de onde?"
            onInputChange={(change) => Context.RequestSuggestions(change, Context.setOrigemOptions)}
            onFocus={() => {
              Context.setOrigemError('');
            }}
            value={Context.origem}
            setValue={Context.setOrigem}
            options={Context.origemOptions}
            setOptions={Context.setOrigemOptions}
            hasError={!!Context.origemError}
            errorMessage={Context.origemError}
            clearable
          />
        ) : (
          <AutoComplete
            id="PASSAGEM-ORIGEM"
            placeholder={'Origem'}
            leftIcon={
              <LocationIcon
                height="1rem"
                width="1.2rem"
                marginLeft="0.3rem"
                color={Context.origemError && ERROR_COLOR}
              />
            }
            label="Partindo de onde?"
            onFocus={() => {
              Context.setOrigemError('');
            }}
            onInputChange={(change) => Context.RequestSuggestions(change, Context.setOrigemOptions)}
            option={Context.origem}
            noOptionsMessage="Digite 3 ou mais letras para pesquisar"
            setOption={Context.setOrigem}
            setOptions={Context.setOrigemOptions}
            value={Context.origemInput}
            setValue={Context.setOrigemInput}
            options={Context.origemOptions}
            hasError={!!Context.origemError}
            errorMessage={Context.origemError}
            clearable
          />
        )}

        {mobile ? (
          <AutoCompleteModal
            id="PASSAGEM-DESTINO"
            placeholder={'Destino'}
            leftIcon={<PlaneIcon marginLeft="0.2rem" height="0.7rem" color={Context.destinoError && ERROR_COLOR} />}
            label="Para qual destino?"
            modalLabel="Para qual destino?"
            onInputChange={(change) => Context.RequestSuggestions(change, Context.setDestinoOptions)}
            onFocus={() => {
              Context.setDestinoError('');
            }}
            value={Context.destino}
            setValue={Context.setDestino}
            options={Context.destinoOptions}
            setOptions={Context.setDestinoOptions}
            hasError={!!Context.destinoError}
            errorMessage={Context.destinoError}
            clearable
          />
        ) : (
          <AutoComplete
            id="PASSAGEM-DESTINO"
            placeholder={'Destino'}
            leftIcon={
              <PlaneIcon
                height="0.7rem"
                width="1.4rem"
                marginLeft="0.2rem"
                color={Context.destinoError && ERROR_COLOR}
              />
            }
            label="Para qual destino?"
            onFocus={() => {
              Context.setDestinoError('');
            }}
            onInputChange={(change) => Context.RequestSuggestions(change, Context.setDestinoOptions)}
            option={Context.destino}
            noOptionsMessage="Digite 3 ou mais letras para pesquisar"
            setOption={Context.setDestino}
            setOptions={Context.setDestinoOptions}
            value={Context.destinoInput}
            setValue={Context.setDestinoInput}
            options={Context.destinoOptions}
            hasError={!!Context.destinoError}
            errorMessage={Context.destinoError}
            clearable
          />
        )}
        <div ref={Context.CalendarRef}>
          <ThinStyle.DateInputs>
            <Input
              id="PASSAGEM-IDA"
              placeholder={'Ida'}
              leftIcon={<CalendarIcon marginLeft="0.2rem" color={Context.startError && ERROR_COLOR} />}
              value={Context.FormatInputDate(Context.start)}
              onClick={() => {
                Context.setCalendarOpen(true);
              }}
              onFocus={() => {
                Context.setStartError('');
                Context.setEndError('');
                Context.setCalendarOpen(true);
                Context.setSelecting('first');
              }}
              onBlur={(e) => {
                if (e.relatedTarget && !e.relatedTarget.contains(Context.CalendarRef.current)) {
                  Context.setCalendarOpen(false);
                }
              }}
              hasError={!!Context.startError}
              errorMessage={Context.startError}
              readOnly
            />
            <Input
              id="PASSAGEM-VOLTA"
              placeholder={'Volta'}
              leftIcon={
                Context.travelType === 'IDA' ? (
                  <CalendarGrayIcon marginLeft="0.2rem" />
                ) : (
                  <CalendarIcon marginLeft="0.2rem" color={Context.endError && ERROR_COLOR} />
                )
              }
              value={Context.travelType !== 'IDA' ? Context.FormatInputDate(Context.end) : ''}
              disabled={Context.travelType === 'IDA'}
              onClick={() => {
                Context.setCalendarOpen(true);
              }}
              onFocus={() => {
                Context.setStartError('');
                Context.setEndError('');
                Context.setCalendarOpen(true);
                Context.setSelecting('last');
              }}
              onBlur={(e) => {
                if (e.relatedTarget && !e.relatedTarget.contains(Context.CalendarRef.current)) {
                  Context.setCalendarOpen(false);
                }
              }}
              hasError={!!Context.endError && Context.travelType !== 'IDA'}
              errorMessage={Context.travelType !== 'IDA' ? Context.endError : ''}
              readOnly
            />
          </ThinStyle.DateInputs>
          {Context.calendarOpen && (
            <div>
              <DatePicker
                start={Context.start}
                setStart={Context.setStart}
                end={Context.end}
                setEnd={Context.setEnd}
                startLimit={Context.startLimit}
                endLimit={Context.endLimit}
                visible={Context.calendarOpen}
                setVisible={Context.setCalendarOpen}
                singleDate={Context.travelType === 'IDA'}
                modalTitle={Context.datePickerModalText}
                isSelecting={Context.selecting}
                setIsSelecting={Context.setSelecting}
                mobile={mobile}
                autoPositionCalendar={autoPositionCalendar}
                multipleCalendars={Context.travelType === 'IDAVOLTA' && multipleCalendars}
              />
            </div>
          )}
        </div>

        <PassengerPicker
          id="PASSAGEM-PASSENGERS"
          label="Passageiros"
          value={`${Context.PassengersTotal} Passageiro${Context.PassengersTotal < 2 ? '' : 's'}`}
          mobile={mobile}
          leftIcon={<PassengerIcon marginLeft="0.2rem" />}
          rightIcon={<ArrowdownIcon marginRight="0.7rem" />}
          values={Context.passengerPickerValues}
          disableDecrement={Context.disableDecrement}
          disableIncrement={Context.disableIncrement}
        />
      </ThinStyle.InputForm>
      <ThinStyle.Submit>
        <Button id="PASSAGEM-PESQUISAR" variant="THIN" onClick={Context.RedirectUrl} rounded>
          PESQUISAR
        </Button>
      </ThinStyle.Submit>
    </>
  );
}
