import React, { useEffect, useState } from 'react';
import { AviaoIcon, CarroIcon, SeguroIcon } from '../icons';
import { Elquarto } from './Elquarto';
import { Passagem } from './Passagem';
import { Seguros } from './Seguro';
import { Container, Frame, Products, Tab } from './styles';

type DefaultSearch = 'PASSAGEM' | 'SEGURO' | 'ELQUARTO';
type FrameTitles = {
  passagem: string;
  elquarto: string;
  seguro: string;
};

type SearchFrameProps = {
  /** Which product will be select by default when the page is loaded, can also be provided on the url by sending ?defaultsearch="PASSAGEM" */
  defaultSearch: DefaultSearch;
  passagemSearchUrl: string;
  segurosSearchUrl: string;
  elquartoSearchUrl: string;
  /** Hides others products tabs, if enabled the user wont be able to switch between products. */
  hideProducts?: boolean;
  /** Shows two calendars instead of one */
  multipleCalendars?: boolean;
  /** If enabled, the calendar will change its direction to top side if screen height is smaller than 900px */
  autoPositionCalendar?: boolean;
  /** If enabled, the current url params will be carried over the search links, in case of duplicated values, url param is prioritized */
  persistUrlParams?: boolean;
  /** When mobile is enabled, some inputs will turn into fullscreen modals */
  mobile?: boolean;
  /** Switch between two layouts */
  size: 'WIDE' | 'THIN';
  /** What text will be shown on search product frame */
  frameTitles: FrameTitles;
};

export function SearchFrame({
  hideProducts,
  defaultSearch,
  multipleCalendars,
  passagemSearchUrl,
  segurosSearchUrl,
  elquartoSearchUrl,
  persistUrlParams,
  frameTitles,
  autoPositionCalendar,
  mobile,
  size,
}: SearchFrameProps) {
  const [product, setProduct] = useState(defaultSearch);

  useEffect(() => {
    const urlSearchParams = new URLSearchParams(window.location.search);
    const urlParam = urlSearchParams.get('defaultsearch') as DefaultSearch;
    switch (urlParam) {
      case 'PASSAGEM':
        setProduct('PASSAGEM');
        break;
      case 'ELQUARTO':
        setProduct('ELQUARTO');
        break;
      case 'SEGURO':
        setProduct('SEGURO');
        break;

      default:
        break;
    }
  }, []);

  return (
    <Container>
      {!hideProducts && (
        <Products className={size}>
          <Tab
            id="TAB-PASSAGEM"
            className={`${size} PRODUCT-TAB`}
            size={size}
            onClick={() => setProduct('PASSAGEM')}
            selected={product === 'PASSAGEM'}
          >
            <AviaoIcon pointerEvents="none" marginRight="0.6rem" />
            Passagens
          </Tab>
          <Tab
            id="TAB-ELQUARTO"
            className={`${size} PRODUCT-TAB`}
            size={size}
            onClick={() => setProduct('ELQUARTO')}
            selected={product === 'ELQUARTO'}
          >
            <CarroIcon pointerEvents="none" marginRight="0.6rem" />
            Hotéis
          </Tab>
          <Tab
            id="TAB-SEGURO"
            className={`${size} PRODUCT-TAB`}
            size={size}
            onClick={() => setProduct('SEGURO')}
            selected={product === 'SEGURO'}
          >
            <SeguroIcon pointerEvents="none" marginRight="0.6rem" />
            Seguros
          </Tab>
        </Products>
      )}

      <Frame className={size}>
        {product === 'PASSAGEM' && (
          <Passagem
            autoPositionCalendar={autoPositionCalendar}
            url={passagemSearchUrl}
            size={size}
            mobile={mobile}
            multipleCalendars={multipleCalendars}
            persistUrlParams={persistUrlParams}
            frameTitle={frameTitles.passagem}
          />
        )}
        {product === 'ELQUARTO' && (
          <Elquarto
            autoPositionCalendar={autoPositionCalendar}
            size={size}
            url={elquartoSearchUrl}
            mobile={mobile}
            multipleCalendars={multipleCalendars}
            persistUrlParams={persistUrlParams}
            frameTitle={frameTitles.elquarto}
          />
        )}
        {product === 'SEGURO' && (
          <Seguros
            autoPositionCalendar={autoPositionCalendar}
            size={size}
            url={segurosSearchUrl}
            mobile={mobile}
            multipleCalendars={multipleCalendars}
            persistUrlParams={persistUrlParams}
            frameTitle={frameTitles.seguro}
          />
        )}
      </Frame>
    </Container>
  );
}
