import React from 'react';
import { Passagem } from '../../SearchFrame/SearchFrame/Passagem';
import { Container, Frame } from './styles';

type PassagemFrameWideProps = {
  SearchUrl: string;
  multipleCalendars?: boolean;
  mobile?: boolean;
  size: 'WIDE' | 'THIN';
};

export function PassagemFrameWide({ multipleCalendars, SearchUrl, mobile, size }: PassagemFrameWideProps) {
  return (
    <Container>
      <Frame>
        <Passagem
          size={size}
          url={SearchUrl}
          mobile={mobile}
          multipleCalendars={multipleCalendars}
          frameTitle="Voe mais por muito menos"
        />
      </Frame>
    </Container>
  );
}
