import React from 'react';
import { PassagemProvider } from './context';
import { PassagemThin } from './thin';
import { PassagemWide } from './wide';

type PassagemProps = {
  size: 'WIDE' | 'THIN';
  url: string;
  mobile?: boolean;
  multipleCalendars?: boolean;
  autoPositionCalendar?: boolean;
  persistUrlParams?: boolean;
  frameTitle: string;
};

export function Passagem({ size, url, persistUrlParams, frameTitle, ...rest }: PassagemProps) {
  return (
    <PassagemProvider frameTitle={frameTitle} url={url} persistUrlParams={persistUrlParams}>
      {size === 'WIDE' ? <PassagemWide {...rest} /> : <PassagemThin {...rest} />}
    </PassagemProvider>
  );
}
