import React, { CSSProperties, InputHTMLAttributes, ReactNode } from 'react';
import { ErrorMessage } from '../ErrorMessage';
import * as S from './styles';

export interface InputProps extends InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  leftIcon?: ReactNode;
  leftIconStyle?: CSSProperties;
  rightIcon?: ReactNode;
  RightIconStyle?: CSSProperties;
  leftIconClick?: () => void;
  rightIconClick?: () => void;
  joinLeft?: boolean;
  joinRight?: boolean;
  noBorderLeft?: boolean;
  noBorderRight?: boolean;
  noBorderTop?: boolean;
  noBorderBottom?: boolean;
  hasError?: boolean;
  errorMessage?: string;
}

export function Input({
  leftIcon,
  rightIcon,
  leftIconStyle,
  RightIconStyle,
  leftIconClick,
  rightIconClick,
  label,
  joinLeft,
  joinRight,
  noBorderBottom,
  noBorderLeft,
  noBorderRight,
  noBorderTop,
  disabled,
  hasError,
  errorMessage,
  ...rest
}: InputProps) {
  return (
    <S.InputContainer>
      {label && <S.Label>{label} </S.Label>}
      <S.InputCenter
        joinLeft={joinLeft}
        joinRight={joinRight}
        noBorderLeft={noBorderLeft}
        noBorderRight={noBorderRight}
        noBorderTop={noBorderTop}
        noBorderBottom={noBorderBottom}
        disabled={disabled}
        hasError={hasError}
      >
        {leftIcon && (
          <S.InputIcon style={leftIconStyle} onClick={leftIconClick}>
            {leftIcon}
          </S.InputIcon>
        )}
        <S.InputStyled hasError={hasError} disabled={disabled} {...rest} />
        {rightIcon && (
          <S.InputIcon style={RightIconStyle} onClick={rightIconClick}>
            {rightIcon}
          </S.InputIcon>
        )}
        <ErrorMessage message={errorMessage} />
      </S.InputCenter>
    </S.InputContainer>
  );
}
