import dayjs from 'dayjs';
import React, { createContext, useState, ReactNode, useContext, useEffect } from 'react';
import { useDetectClickOutside } from 'react-detect-click-outside';
import { IAutoComplete } from '../../AutoComplete';
import { DateRangePickerSelecting } from '../../DateRangePicker';
import { AirportIcon } from '../../icons';
import { PassengerPickerRules, PassengerPickerValues } from '../../PassengerPicker';
import { FormatDateTo, PersistAndReplaceUrlParams, RequestSuggestionsPassagem } from '../../utils';
import {
  ERROR_COLOR,
  PASSAGEM_DATE_LIMIT_MONTHS,
  PASSAGEM_DEFAULT_AMOUNT_ADULT,
  PASSAGEM_MAX_PASSENGERS,
  PASSAGEM_MIN_ADULTS,
} from '../../variables';

interface IPassagemContextProps {
  adults: number;
  childs: number;
  infants: number;
  start: any;
  end: any;
  endLimit: Date;
  startLimit: Date;
  startError: string;
  endError: string;
  origem: IAutoComplete.Value | null;
  origemError: string;
  origemInput: string;
  destino: IAutoComplete.Value | null;
  destinoError: string;
  destinoInput: string;
  calendarOpen: boolean;
  travelType: TravelType;
  origemOptions: IAutoComplete.CategoryOptions[];
  destinoOptions: IAutoComplete.CategoryOptions[];
  PassengersTotal: number;
  PassengersLimit: number;
  CalendarRef: any;
  passengerPickerValues: PassengerPickerValues;
  disableIncrement: PassengerPickerRules;
  disableDecrement: PassengerPickerRules;
  selecting: DateRangePickerSelecting | undefined;
  datePickerModalText: string;
  frameTitle: string;
  setSelecting: (selecting: DateRangePickerSelecting | undefined) => void;
  setStart: (start: any) => void;
  setEnd: (end: any) => void;
  setStartError: (error: string) => void;
  setEndError: (error: string) => void;
  setAdults: (val: number) => void;
  setChilds: (val: number) => void;
  setCalendarOpen: (val: boolean) => void;
  setInfants: (val: number) => void;
  setOrigem: (val: IAutoComplete.Value | null) => void;
  setOrigemError: (error: string) => void;
  setOrigemInput: (val: string) => void;
  setDestino: (val: IAutoComplete.Value | null) => void;
  setDestinoError: (error: string) => void;
  setDestinoInput: (val: string) => void;
  setTravelType: (type: TravelType) => void;
  setOrigemOptions: (options: IAutoComplete.CategoryOptions[]) => void;
  setDestinoOptions: (options: IAutoComplete.CategoryOptions[]) => void;
  FormatInputDate: (d: Date | null) => string;
  RedirectUrl: () => void;
  RequestSuggestions: (value: string, setOptions: (options: any) => void) => void;
}

type PassagemProviderProps = {
  children: ReactNode;
  url: string;
  persistUrlParams?: boolean;
  frameTitle: string;
};

type TravelType = 'IDAVOLTA' | 'IDA' | 'MULTI';

export const PassagemContext = createContext<IPassagemContextProps>({} as any);
export const UsePassagem = () => useContext(PassagemContext);
export function PassagemProvider({ children, url, persistUrlParams, frameTitle }: PassagemProviderProps) {
  const [isMounted, setIsMounted] = useState(true);
  const [start, setStart] = useState<any>(undefined);
  const [end, setEnd] = useState<any>(undefined);
  const [startError, setStartError] = useState('');
  const [endError, setEndError] = useState('');
  const startLimit = new Date();
  const endLimit = dayjs(startLimit).add(PASSAGEM_DATE_LIMIT_MONTHS, 'month').toDate();
  const [adults, setAdults] = useState(PASSAGEM_DEFAULT_AMOUNT_ADULT);
  const [childs, setChilds] = useState(0);
  const [infants, setInfants] = useState(0);
  const [origem, setOrigem] = useState<IAutoComplete.Value | null>(null);
  const [origemError, setOrigemError] = useState('');
  const [origemInput, setOrigemInput] = useState('');
  const [destino, setDestino] = useState<IAutoComplete.Value | null>(null);
  const [destinoError, setDestinoError] = useState('');
  const [destinoInput, setDestinoInput] = useState('');
  const [calendarOpen, setCalendarOpen] = useState(false);
  const [selecting, setSelecting] = useState<DateRangePickerSelecting | undefined>(undefined);
  const [travelType, setTravelType] = useState<TravelType>('IDAVOLTA');
  const [origemOptions, setOrigemOptions] = useState<IAutoComplete.CategoryOptions[]>([]);
  const [destinoOptions, setDestinoOptions] = useState<IAutoComplete.CategoryOptions[]>([]);
  const PassengersTotal = adults + childs + infants;
  const PassengersLimit = PASSAGEM_MAX_PASSENGERS;
  const CalendarRef = useDetectClickOutside({
    onTriggered: () => setCalendarOpen(false),
  });

  useEffect(() => {
    setIsMounted(true);
    return () => {
      setIsMounted(false);
    };
  }, []);

  const datePickerModalText = (function () {
    let text = 'Escolha a data de ';
    switch (selecting) {
      case 'first':
        text += 'Ida';
        return text;
      case 'last':
        text += 'Volta';
        return text;
    }
    if (!start && !end) {
      text += 'Ida';
      return text;
    }
    if (start && !end) {
      text += 'Volta';
      return text;
    }
    return text;
  })();

  const disableDecrement: PassengerPickerRules = {
    adults: adults <= PASSAGEM_MIN_ADULTS || infants >= adults,
    childs: childs <= 0,
    infants: infants <= 0,
  };

  const disableIncrement: PassengerPickerRules = {
    adults: PassengersTotal >= PASSAGEM_MAX_PASSENGERS,
    childs: PassengersTotal >= PASSAGEM_MAX_PASSENGERS,
    infants: infants >= adults || PassengersTotal >= PASSAGEM_MAX_PASSENGERS,
  };

  const passengerPickerValues: PassengerPickerValues = {
    adults,
    childs,
    infants,
    setAdults,
    setChilds,
    setInfants,
  };

  function FormatInputDate(d: Date | null): string {
    if (d) return FormatDateTo(d, 'DD/MM/YYYY');
    return '';
  }

  function VerifyFields(): boolean {
    let hasErrors = false;
    if (!start) {
      setStartError('Insira a data');
      hasErrors = true;
    }
    if (!end && travelType === 'IDAVOLTA') {
      setEndError('Insira a data');
      hasErrors = true;
    }
    if (!origem?.value || !(origem?.value !== '')) {
      setOrigemError('Origem invalida');
      hasErrors = true;
    }
    if (!destino?.value || !(destino?.value !== '')) {
      setDestinoError('Destino invalido');
      hasErrors = true;
    }

    if (destino?.value.length > 0 && origem?.value === destino?.value) {
      setDestinoError('Destino não pode ser igual a origem');
      hasErrors = true;
    }
    return hasErrors;
  }

  function RedirectUrl() {
    if (!VerifyFields()) {
      let searchString = '';
      const dataIda = FormatDateTo(start, 'YYMMDD');
      const idaVolta = `${origem?.value}${destino?.value}`;
      searchString += idaVolta + dataIda;
      if (travelType === 'IDAVOLTA') {
        const dataVolta = FormatDateTo(end, 'YYMMDD');
        const voltaIda = `${destino?.value}${origem?.value}`;
        searchString += '-' + voltaIda + dataVolta;
      }
      const urlParams = persistUrlParams ? `?${PersistAndReplaceUrlParams('')}` : '';

      window.open(`${url}/air/search/${searchString}/${adults}/${childs}/${infants}/Y/${urlParams}`, '_blank');
    }
  }

  async function RequestSuggestions(value: string, setOptions: (options: any) => void) {
    if (value.length < 3 && isMounted) {
      setOrigemOptions([]);
      return;
    }

    if (value.length >= 3 && isMounted) {
      const searchOptions = await RequestSuggestionsPassagem(value);
      if (searchOptions.length < 1) {
        return setOptions([
          {
            categoryName: 'Aeroporto invalido',
            categoryIconUrl: <AirportIcon width="1.5rem" height="1.5rem" color={ERROR_COLOR} />,
            categoryColor: '#fff',
            categoryLabelColor: ERROR_COLOR,
            options: searchOptions,
          },
        ]);
      }
      setOptions([
        {
          categoryName: 'Aeroportos',
          categoryIconUrl: <AirportIcon width="1.5rem" height="1.5rem" />,
          categoryColor: '#008BFF',
          categoryLabelColor: '#fff',
          options: searchOptions,
        },
      ]);
    }
  }

  const DefaultState: IPassagemContextProps = {
    adults,
    childs,
    infants,
    start,
    end,
    endLimit,
    startLimit,
    startError,
    endError,
    origem,
    origemError,
    origemInput,
    destino,
    destinoError,
    destinoInput,
    calendarOpen,
    travelType,
    origemOptions,
    destinoOptions,
    PassengersTotal,
    PassengersLimit,
    CalendarRef,
    disableIncrement,
    disableDecrement,
    passengerPickerValues,
    selecting,
    datePickerModalText,
    frameTitle,
    setSelecting,
    setStart,
    setEnd,
    setCalendarOpen,
    setStartError,
    setEndError,
    setAdults,
    setChilds,
    setInfants,
    setOrigem,
    setOrigemError,
    setOrigemInput,
    setDestino,
    setDestinoError,
    setDestinoInput,
    setTravelType,
    setOrigemOptions,
    setDestinoOptions,
    FormatInputDate,
    RedirectUrl,
    RequestSuggestions,
  };

  return <PassagemContext.Provider value={DefaultState}>{children}</PassagemContext.Provider>;
}
