import dayjs from 'dayjs';
import React, { createContext, useState, ReactNode, useContext } from 'react';
import { useDetectClickOutside } from 'react-detect-click-outside';
import { DateRangePickerSelecting } from '../../DateRangePicker';
import { collectData, FormatDateTo, PersistAndReplaceUrlParams } from '../../utils';
import { SEGUROS_DATE_LIMIT_MONTHS } from '../../variables';

interface ISegurosContextProps {
  start: any;
  end: any;
  startError: string;
  endError: string;
  destino: string;
  destinoError: string;
  startLimit: Date;
  endLimit: Date;
  calendarOpen: boolean;
  CalendarRef: any;
  Options: string[];
  selecting?: DateRangePickerSelecting;
  datePickerModalText: string;
  frameTitle: string;
  setSelecting: (val: DateRangePickerSelecting) => void;
  setStart: (val: any) => void;
  setEnd: (val: any) => void;
  setStartError: (val: string) => void;
  setEndError: (val: string) => void;
  setDestino: (val: string) => void;
  setDestinoError: (val: string) => void;
  setCalendarOpen: (val: boolean) => void;
  RedirectUrl: () => void;
}

type SegurosProviderProps = {
  children: ReactNode;
  url: string;
  persistUrlParams?: boolean;
  frameTitle: string;
};

export const SegurosContext = createContext<ISegurosContextProps>({} as any);
export const UseSeguros = () => useContext(SegurosContext);
export function SegurosProvider({ children, url, persistUrlParams, frameTitle }: SegurosProviderProps) {
  const [start, setStart] = useState<any>(undefined);
  const [end, setEnd] = useState<any>(undefined);
  const [startError, setStartError] = useState('');
  const [endError, setEndError] = useState('');
  const [destino, setDestino] = useState('');
  const [destinoError, setDestinoError] = useState('');
  const [selecting, setSelecting] = useState<DateRangePickerSelecting | undefined>(undefined);
  const startLimit = new Date();
  const endLimit = dayjs(startLimit).add(SEGUROS_DATE_LIMIT_MONTHS, 'month').toDate();
  const [calendarOpen, setCalendarOpen] = useState(false);
  const CalendarRef = useDetectClickOutside({
    onTriggered: () => setCalendarOpen(false),
  });

  const datePickerModalText = (function () {
    let text = 'Escolha a data de ';
    switch (selecting) {
      case 'first':
        text += 'embarque';
        return text;
      case 'last':
        text += 'desembarque';
        return text;
    }
    if (!start && !end) {
      text += 'embarque';
      return text;
    }
    if (start && !end) {
      text += 'desembarque';
      return text;
    }
    return text;
  })();

  function VerifyFields(): boolean {
    let hasErrors = false;
    if (!start) {
      setStartError('Insira a data');
      hasErrors = true;
    }
    if (!end) {
      setEndError('Insira a data');
      hasErrors = true;
    }
    if (!destino) {
      setDestinoError('Selecione um destino');
      hasErrors = true;
    }

    return hasErrors;
  }

  function RedirectUrl() {
    if (!VerifyFields()) {
      const dataIda = FormatDateTo(start, 'YYYY-MM-DD');
      const dataVolta = FormatDateTo(end, 'YYYY-MM-DD');
      const destinoFormat = destino
        .toLowerCase()
        .normalize('NFD')
        .replace(/[\u0300-\u036f]/g, '')
        .replaceAll(' ', '-');
      const defaultParams =
        'origin=pp&cupom=SUPERPROMO&pcrtt=FRAME_PASSAGENSPROMO&pcrid=135&utm_medium=afiliado&utm_source=FRAME_PASSAGENSPROMO';
      const urlParams = persistUrlParams ? PersistAndReplaceUrlParams(defaultParams) : defaultParams;
      window.open(`${url}/p/passagenspromo/pesquisa/${destinoFormat}/${dataIda}/${dataVolta}/?${urlParams}`, '_blank');

      // gtag datalayer data collect
      const dataArray = [destinoFormat, dataIda, dataVolta];
      const label = dataArray.join('|');
      collectData({
        category: 'pp-frame-pesquisa',
        action: 'pesquisa-seguros',
        label,
      });
    }
  }

  const Options = [
    'Brasil',
    'América do Sul',
    'Europa',
    'América do Norte',
    'América Central',
    'Oceania',
    'Oriente Médio',
    'Ásia',
    'África',
    'Marítimo',
    'Internacional',
  ];

  const DefaultState: ISegurosContextProps = {
    start,
    end,
    startError,
    endError,
    destino,
    destinoError,
    startLimit,
    endLimit,
    calendarOpen,
    CalendarRef,
    Options,
    selecting,
    datePickerModalText,
    frameTitle,
    setSelecting,
    setStart,
    setEnd,
    setStartError,
    setEndError,
    setDestino,
    setDestinoError,
    setCalendarOpen,
    RedirectUrl,
  };

  return <SegurosContext.Provider value={DefaultState}>{children}</SegurosContext.Provider>;
}
