import React, { CSSProperties, ReactNode, useRef, useState } from 'react';
import { useDetectClickOutside } from 'react-detect-click-outside';
import * as S from './styles';
import { SmallCloseIcon } from '../icons';
import { ErrorMessage } from '../ErrorMessage';

export declare namespace IAutoComplete {
  type Value = {
    value: any;
    label: string;
    isDisabled?: boolean;
    props?: any;
  };

  type CategoryOptions = {
    categoryName: string;
    categoryIconUrl: ReactNode;
    categoryColor: string;
    categoryLabelColor: string;
    options: Value[];
  };
}

export interface AutoCompleteProps {
  setOptions: (options: IAutoComplete.CategoryOptions[]) => void;
  onInputChange: (value: string) => void;
  onFocus?: (value: string) => void;
  onBlur?: (value: string) => void;
  leftIconClick?: () => void;
  clearable?: boolean;
  option?: IAutoComplete.Value | null;
  setOption: (option: IAutoComplete.Value | null) => void;
  value: string;
  setValue: (value: string) => void;
  options: IAutoComplete.CategoryOptions[];
  noOptionsMessage?: string;
  label?: string;
  id?: string;
  placeholder?: string;
  disabled?: boolean;
  leftIcon?: ReactNode;
  leftIconStyle?: CSSProperties;
  joinLeft?: boolean;
  joinRight?: boolean;
  noBorderLeft?: boolean;
  noBorderRight?: boolean;
  noBorderTop?: boolean;
  noBorderBottom?: boolean;
  hasError?: boolean;
  errorMessage?: string;
}

export function AutoComplete({
  leftIcon,
  leftIconStyle,
  leftIconClick,
  label,
  joinLeft,
  joinRight,
  noBorderBottom,
  noBorderLeft,
  noBorderRight,
  noBorderTop,
  disabled,
  id,
  options,
  errorMessage,
  clearable,
  noOptionsMessage,
  setOptions,
  setOption,
  onInputChange,
  onFocus,
  onBlur,
  value,
  setValue,
  hasError,
  placeholder,
}: AutoCompleteProps) {
  const [focused, setFocused] = useState(false);
  const [hoveringOption, setHoveringOption] = useState('O1');
  const dropdownRef = useRef(null);
  const centerRef = useRef<HTMLInputElement>(null);
  const parsedOptions = ParseOptionsToCategoryAndOptions(options);
  const availableOptions = parsedOptions?.filter((option) => option.isDisabled === false);
  const avaiableOptionsLength = availableOptions.length;
  const InputRef = useDetectClickOutside({
    onTriggered: HandleClickOutside,
  });

  function StripLabelIata(label: string) {
    const splitValue = label.split('(');
    if (splitValue.length < 2)
      return {
        label,
        iata: '',
      };
    const labelText = splitValue[0];
    const iataText = '(' + splitValue[1];
    return {
      label: labelText,
      iata: iataText,
    };
  }

  function handleKeyPress(e: React.KeyboardEvent<HTMLInputElement>) {
    const option = Number(hoveringOption.replace(/\D/g, ''));
    if (e.key === 'ArrowUp' && option > 1) {
      const newIndex = `O${option - 1}`;
      setHoveringOption(newIndex);
    }
    if (e.key === 'ArrowDown' && avaiableOptionsLength > option) {
      const newIndex = `O${option + 1}`;
      setHoveringOption(newIndex);
    }
    if (e.key === 'Enter') {
      const selected = availableOptions[option - 1];
      if (selected) {
        const { label } = selected;
        setValue(label);
        OnOptionSelected(selected);
      }
    }
  }

  function ParseOptionsToCategoryAndOptions(options: IAutoComplete.CategoryOptions[]) {
    // array for receiving all options
    let AllOptions: IAutoComplete.Value[] = [];
    // here we loop on all options creating a category for each one
    options.forEach((opt) => {
      // the first value in the array is a disabled option, to act as a category
      const CategoryArray = [
        {
          value: {
            categoryColor: opt.categoryColor,
            categoryLabelColor: opt.categoryLabelColor,
            categoryIconUrl: opt.categoryIconUrl,
          },
          label: opt.categoryName,
          isDisabled: true,
        },
        // then we spread the actual options
        ...opt.options,
      ];
      // and finally we join this section with the rest of the options
      AllOptions = [...AllOptions, ...CategoryArray];
    });
    return AllOptions;
  }

  function RenderAllOptions(parsedOptions: IAutoComplete.Value[]) {
    let categoryIndex = 0;
    let optionIndex = 0;

    const optionsArray = parsedOptions.map((option) => {
      const { isDisabled } = option;
      if (isDisabled) {
        categoryIndex++;
        return RenderCategory(option, categoryIndex);
      }
      optionIndex++;
      return RenderOption(option, optionIndex);
    });
    return optionsArray;
  }

  function RenderCategory(props: IAutoComplete.Value, index: number) {
    const { label, value } = props;
    const key = 'C' + index;
    return (
      <S.CustomCategory key={key} id={key} style={{ backgroundColor: value.categoryColor }}>
        {value?.categoryIconUrl && value.categoryIconUrl}
        <S.CustomOptionCategory color={value.categoryLabelColor}>{label}</S.CustomOptionCategory>
      </S.CustomCategory>
    );
  }

  function RenderOption(option: IAutoComplete.Value, index: number) {
    const { label } = option;
    const text = StripLabelIata(label);
    const key = 'O' + index;
    return (
      <S.CustomOptionContainer
        className={hoveringOption === key ? 'selected' : ''}
        key={key}
        id={key}
        onMouseEnter={() => {
          setHoveringOption(key);
        }}
        onClick={() => {
          OnOptionSelected(option);
        }}
      >
        <S.CustomOption>
          {text.label} <strong>{text.iata}</strong>
        </S.CustomOption>
      </S.CustomOptionContainer>
    );
  }

  function RenderNoOptionMessage(message: string) {
    return (
      <S.CustomCategory key={0} style={{ backgroundColor: '#fff' }}>
        <S.NoOption>{message}</S.NoOption>
      </S.CustomCategory>
    );
  }

  function OnOptionSelected(option: IAutoComplete.Value) {
    setOption(option);
    setValue(option.label);
    setFocused(false);
  }
  function HandleOnChange(e: React.ChangeEvent<HTMLInputElement>) {
    setValue(e.target.value);
    onInputChange(e.target.value);
    if (!focused) setFocused(true);
  }
  function HandleClickOutside() {
    setFocused(false);
    setOptions([]);
  }

  function HandleBlur(e: React.FocusEvent<HTMLInputElement, Element>) {
    const hoverOption = Number(hoveringOption.replace(/\D/g, ''));
    const selected = availableOptions[hoverOption - 1];
    if (selected) {
      const { label } = selected;
      setValue(label);
      OnOptionSelected(selected);
    }

    if (!e.currentTarget.contains(e.relatedTarget || dropdownRef.current)) {
      setFocused(false);
    }
    onBlur && onBlur(value);
  }
  function HandleFocus() {
    setFocused(true);
    onFocus && onFocus(value);
  }
  return (
    <S.InputContainer visible={focused} ref={InputRef} onFocus={HandleFocus} onBlur={HandleBlur}>
      {label && <S.Label>{label} </S.Label>}
      <S.InputCenter
        joinLeft={joinLeft}
        joinRight={joinRight}
        noBorderLeft={noBorderLeft}
        noBorderRight={noBorderRight}
        noBorderTop={noBorderTop}
        noBorderBottom={noBorderBottom}
        disabled={disabled}
        hasError={hasError}
      >
        <ErrorMessage message={errorMessage} />
        {leftIcon && (
          <S.InputIcon style={leftIconStyle} onClick={leftIconClick}>
            {leftIcon}
          </S.InputIcon>
        )}
        <S.InputStyled
          id={id}
          data-testid={`autocomplete-${id}`}
          ref={centerRef}
          placeholder={placeholder}
          value={value}
          hasError={hasError}
          disabled={disabled}
          onChange={HandleOnChange}
          onFocus={(e) => e.target.select()}
          onKeyUp={handleKeyPress}
          autoComplete="off"
        />
        {clearable && !!value && (
          <S.InputIcon
            id={`clear-autocomplete-${id}`}
            onClick={() => {
              setValue('');
              setOption(null);
            }}
          >
            <SmallCloseIcon height="1rem" width="1.5rem" marginLeft="0.3rem" color="#A8A8A8" />
          </S.InputIcon>
        )}
      </S.InputCenter>
      <S.OptionsWrapper ref={dropdownRef} style={{ display: focused ? '' : 'none' }}>
        <S.OptionsList joinLeft={joinLeft} joinRight={joinRight}>
          {RenderAllOptions(parsedOptions)}
          {parsedOptions.length < 1 && noOptionsMessage && RenderNoOptionMessage(noOptionsMessage)}
        </S.OptionsList>
      </S.OptionsWrapper>
    </S.InputContainer>
  );
}
