import React from 'react';
import { Input } from '../../Input';
import { DatePicker } from '../../DatePicker';
import { AutoComplete } from '../../AutoComplete';
import { WideStyle } from './styles';
import { Button } from '../../Button';
import * as Icons from '../../icons';
import { PassengerPicker } from '../../PassengerPicker';
import { ERROR_COLOR } from '../../variables';
import { UseElquarto } from './context';

type ElquartoProps = {
  mobile?: boolean;
  multipleCalendars?: boolean;
  autoPositionCalendar?: boolean;
};

export function ElquartoWide({ mobile, multipleCalendars, autoPositionCalendar }: ElquartoProps) {
  const Context = UseElquarto();
  return (
    <WideStyle.Container>
      <WideStyle.Header>
        <WideStyle.HeaderText>{Context.frameTitle}</WideStyle.HeaderText>
        <WideStyle.HeaderImage>
          <Icons.ElquartowhiteIcon />
        </WideStyle.HeaderImage>
      </WideStyle.Header>
      <WideStyle.ContentContainer>
        <WideStyle.ContentLine>
          <AutoComplete
            id="ELQUARTO-DESTINO"
            placeholder={'Para onde você vai?'}
            leftIcon={
              <Icons.LocationIcon marginLeft="0.3rem" height="1.5rem" color={Context.destinoError && ERROR_COLOR} />
            }
            onFocus={() => {
              Context.setDestinoError('');
            }}
            onInputChange={(change) => Context.RequestSuggestions(change, Context.setDestinoOptions)}
            option={Context.destino}
            noOptionsMessage="Digite 3 ou mais letras para pesquisar"
            setOption={Context.setDestino}
            setOptions={Context.setDestinoOptions}
            options={Context.destinoOptions}
            value={Context.destinoInput}
            setValue={Context.setDestinoInput}
            hasError={!!Context.destinoError}
            errorMessage={Context.destinoError}
            clearable
          />
        </WideStyle.ContentLine>

        <WideStyle.Gridline columns="2fr 2fr 1fr">
          <WideStyle.CalendarWrapper>
            <div ref={Context.CalendarRef}>
              <Input
                id="ELQUARTO-CHECKIN-CHECKOUT"
                placeholder={'Check-in e check-out'}
                leftIcon={
                  <Icons.CalendarIcon
                    marginLeft="0.3rem"
                    height="1.5rem"
                    color={!!Context.endError || !!Context.endError ? ERROR_COLOR : ''}
                  />
                }
                value={Context.formatCheckinValue()}
                onClick={() => {
                  Context.setCalendarOpen(true);
                }}
                onFocus={() => {
                  Context.setStartError('');
                  Context.setEndError('');
                  Context.setCalendarOpen(true);
                }}
                onBlur={(e) => {
                  if (e.relatedTarget && !e.relatedTarget.contains(Context.CalendarRef.current)) {
                    Context.setCalendarOpen(false);
                  }
                }}
                hasError={!!Context.endError || !!Context.endError}
                errorMessage={Context.startError || Context.endError}
                readOnly
                joinRight
              />
              {Context.calendarOpen && (
                <DatePicker
                  autoPositionCalendar={autoPositionCalendar}
                  start={Context.start}
                  setStart={Context.setStart}
                  end={Context.end}
                  setEnd={Context.setEnd}
                  visible={Context.calendarOpen}
                  startLimit={Context.startLimit}
                  setVisible={Context.setCalendarOpen}
                  mobile={mobile}
                  multipleCalendars={multipleCalendars}
                />
              )}
            </div>
          </WideStyle.CalendarWrapper>
          <PassengerPicker
            id="ELQUARTO-PASSENGERS"
            value={`${Context.rooms} Quarto${Context.rooms > 1 ? 's' : ''} , ${Context.PassengersTotal} Hóspede${
              Context.PassengersTotal > 1 ? 's' : ''
            }`}
            leftIcon={
              <Icons.CamaIcon marginLeft="0.3rem" height="1.5rem" color={Context.childsAgeError ? ERROR_COLOR : ''} />
            }
            rightIcon={<Icons.ArrowdownIcon marginRight="0.7rem" />}
            hasError={!!Context.childsAgeError}
            errorMessage={Context.childsAgeError}
            onFocus={() => {
              Context.setChildsAgeError('');
            }}
            joinLeft
            joinRight
            noBorderLeft
            noBorderRight
            childsAge={Context.childsAge}
            setChildsAge={Context.setChildsAge}
            values={Context.passengerPickerValues}
            disableDecrement={Context.disableDecrement}
            disableIncrement={Context.disableIncrement}
            infoLabel={Context.infoLabel}
          />
          <div
            style={{
              paddingRight: '0.5rem',
              marginLeft: '-1px',
            }}
          >
            <Button id="ELQUARTO-PESQUISAR" variant="WIDE" onClick={Context.RedirectUrl} joinLeft>
              PESQUISAR
            </Button>
          </div>
        </WideStyle.Gridline>
      </WideStyle.ContentContainer>
    </WideStyle.Container>
  );
}
